import {Component, OnInit} from "@angular/core";
import {PageSet} from "@component/wizard/flow-wizard.component";
import {ClaimDisksWitnessHostPage} from "@component/wizard/shared-pages/claim-disks-witness-host-page.component";
import {FaultDomainsPageComponent} from "@component/wizard/shared-pages/fault-domains-page.component";
import {SingleSiteFaultDomainsPage} from "@component/wizard/shared-pages/single-site-fault-domains-page.component";
import {WitnessHostPageComponent} from "@component/wizard/shared-pages/witness-host-page.component";
import {ClusterConfigData} from "@generated/cluster-config-data";
import {ConfigureClusterService} from "@generated/configure-cluster-service";
import {ConfigureStretchedClusterService} from "@generated/configure-stretched-cluster-service";
import {ConfigureWizardData} from "@generated/configure-wizard-data";
import {EncryptionPropertyProvider} from "@generated/encryption-property-provider";
import {HciClusterService} from "@generated/hci-cluster-service";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {VsanClusterType} from "@generated/vsan-cluster-type";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {HciConfigureWizardContext} from "@navigation/context/hci-configure-wizard-context";
import {ManagedObject} from "@service/managed-object";
import {NetworkProxyConfig, NetworkProxyService} from "@service/vapi/network-proxy.service";
import {Logger} from "@util/logger";
import {WebPlatform} from "@util/platform";
import {VsanQuickstartUtil} from "@util/vsan-quickstart-util";
import {VsanUiUtils} from "@util/vsan-util";
import {WitnessHostUtil} from "@util/witness-host.util";
import {ConfigureHciService} from "./service/configure-hci.service";
import {AdvancedOptionsPageComponent} from "./view/advanced-options-page.component";
import {HciClaimDisksPage} from "./view/claim-disks-page.component";
import {DistributedSwitchesPageComponent} from "./view/distributed-switches-page.component";
import {HciClusterReadyToCompletePageComponent} from "./view/hci-cluster-ready-to-complete-page.component";
import {ProxySettingsPageComponent} from "./view/proxy-settings-page.component";
import {StorageTrafficPageComponent} from "./view/storage-traffic-page.component";
import {SupportInsightPageComponent} from "./view/support-insight-page.component";
import {VmotionTrafficPageComponent} from "./view/vmotion-traffic-page.component";

@Component({
   selector: "vsan-configure-hci-cluster",
   templateUrl: "configure-hci-cluster-wizard.html",
   styleUrls: ["configure-hci-cluster-wizard.scss"],
})
export class ConfigureHciClusterWizard implements OnInit {
   private configureWizardData: ConfigureWizardData;
   private clusterCapabilityData: VsanCapabilityData;
   private vcCapabilityData: VsanCapabilityData;
   private clusterConfigData: ClusterConfigData;
   private httpsProxySetting: NetworkProxyConfig;

   dataLoaded: boolean = false;

   context: any = {};
   services: any[] = [
      HciClusterService,
      ConfigureClusterService,
      ConfigureStretchedClusterService,
      VsanStretchedClusterService,
      ConfigureHciService];
   pages: PageSet[] = [];

   constructor(private hciService: HciClusterService,
         private capabilityProvider: VsanCapabilityProvider,
         private networkProxyService: NetworkProxyService,
         private encryptionPropertyProvider: EncryptionPropertyProvider) {
   }

   async ngOnInit(): Promise<void> {
      const modalContext: HciConfigureWizardContext = VsanUiUtils.getModalContext();
      this.configureWizardData = modalContext.configureWizardData;
      const isVsanMaxSupportedOnHosts: boolean = modalContext.isVsanMaxSupportedOnHosts;

      // assign a11y title here due to additional loading logic
      WebPlatform.setAccessibilityTitle(VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.title"));
      let hasEncryptionPermissions: boolean;
      let hasRekeyPermission: boolean;
      [
         hasEncryptionPermissions,
         hasRekeyPermission,
         this.clusterConfigData,
         this.clusterCapabilityData,
         this.vcCapabilityData,
      ] = await Promise.all([
         this.encryptionPropertyProvider.getEncryptionPermissions(ManagedObject.contextObject),
         this.encryptionPropertyProvider.getReKeyPermissions(ManagedObject.contextObject),
         this.hciService.getClusterConfigData(ManagedObject.contextObject),
         this.capabilityProvider.getClusterCapabilityData(ManagedObject.contextObject),
         this.capabilityProvider.getVcCapabilityData(ManagedObject.contextObject),
      ]);
      if (this.clusterConfigData !== null) {
         // query Support Insight Proxy settings only when the following prerequisites are met:
         //    1. encryption permissions are granted
         //    2. vSAN is enabled
         //    3. is in the creation cluster wizard
         //    4. this feature is supported
         if (hasEncryptionPermissions && this.clusterConfigData.basicConfig.vsanEnabled
               && VsanQuickstartUtil.isHciWorkflowInProgress(this.clusterConfigData.basicConfig.hciWorkflowState)
               && this.clusterCapabilityData.isSupportInsightSupported) {
            await this.networkProxyService.getSettings(ManagedObject.contextObject)
                  .then(httpsProxySetting => this.httpsProxySetting = httpsProxySetting)
                  .catch(error => Logger.error("Failed to query VC proxy settings", error));
         }

         this.context = {
            moRef: ManagedObject.contextObject,
            clusterRef: ManagedObject.contextObject,
            contextClusterRef: ManagedObject.contextObject,
            configureWizardData: this.configureWizardData,
            clusterHosts: this.configureWizardData.hosts,
            notConfiguredClusterHosts: this.configureWizardData.notConfiguredHosts,
            clusterConfigData: this.clusterConfigData,
            hasDiskGroupsOnWitness: false,
            isVsanMaxSupportedOnHosts: isVsanMaxSupportedOnHosts,
            // Needed for hostsClaimingData InputProperty's retrieval in claim-disks page.
            isVsanMaxWorkflow: this.clusterConfigData.vsanConfigSpec.isVsanMaxEnabled,

            // This spec is used for all vsan specific configs. All vSAN pages + Advanced options page
            // use this spec which is copied back to the clusterConfigData in the ready to complete page.
            // This workaround is needed in order to keep the configure vSAN and configure stretch cluster wizards
            // separate from the configure HCI cluster wizard
            vsanConfigSpec: this.clusterConfigData.vsanConfigSpec,
            hciWorkflow: true,
            vcCapabilityData: this.vcCapabilityData,
            clusterCapabilityData: this.clusterCapabilityData,
            encryptionPermissions: hasEncryptionPermissions,
            rekeyPermission: hasRekeyPermission,
         };

         this.pages = this.getPages();
      }
      this.dataLoaded = true;
   }

   private getPages(): PageSet[] {
      const pages: PageSet[] = [
         new PageSet([DistributedSwitchesPageComponent],
               () => this.configureWizardData.showDvsPage),
         new PageSet([VmotionTrafficPageComponent],
               () => this.configureWizardData.showVmotionTrafficPage && !this.configureWizardData.optOutOfNetworking),
         new PageSet([StorageTrafficPageComponent],
               () => this.configureWizardData.showVsanTrafficPage && !this.configureWizardData.optOutOfNetworking),
         new PageSet([AdvancedOptionsPageComponent],
               () => this.configureWizardData.showAdvancedOptionsPage),

         new PageSet([HciClaimDisksPage],
               () => this.configureWizardData.showClaimDisksPage),
      ];
      /* Include the Support Insight configuration page statically. E.g. we do not want it to disappear when the
       user click the "Hide this page" checkbox in it.
       1. Proxy setting information is collected
       2. SupportInsight is supported and enabled
       3. CEIP is not enabled.
       */
      if (this.httpsProxySetting
            && !this.configureWizardData.isSupportInsightStepHidden
            && !this.configureWizardData.ceipEnabled) {
         pages.push(new PageSet([SupportInsightPageComponent]));
      }

      pages.push(...[
         /* Include the proxy setting view when:
          1. SupportInsight is supported and enabled
          2. User hasn't checked the 'Hide support insight' checkbox
          3. Proxy setting information is collected and proxy is not set
         */
         new PageSet([ProxySettingsPageComponent],
               () => !this.configureWizardData.isSupportInsightStepHidden
                     && !this.configureWizardData.disableSupportInsight
                     && this.httpsProxySetting
                     && !this.httpsProxySetting.enabled),
         new PageSet([FaultDomainsPageComponent],
               () => this.configureWizardData.selectedVsanClusterType === VsanClusterType.STRETCHED_CLUSTER),
      ]);

      // In extend mode the witness host is already configured, so skip related pages.
      if (!this.configureWizardData.isExtend) {
         pages.push(...[
            new PageSet([WitnessHostPageComponent],
                  () => this.configureWizardData.selectedVsanClusterType === VsanClusterType.STRETCHED_CLUSTER
                        || this.configureWizardData.selectedVsanClusterType === VsanClusterType.TWO_HOST_VSAN_CLUSTER),
            new PageSet([ClaimDisksWitnessHostPage], this.includeClaimDisksPage),
         ]);
      }
      pages.push(...[
         new PageSet([SingleSiteFaultDomainsPage],
               () => this.configureWizardData.showSingleSiteFaultDomainsPage),
         new PageSet([HciClusterReadyToCompletePageComponent]),
      ]);
      return pages;
   }

   private includeClaimDisksPage = (
         hasDiskGroupsOnWitness: boolean,
         isWitnessDeployedFromOvf: boolean,
         isVsanMaxWorkflow: boolean): boolean => {
      return WitnessHostUtil.isClaimDisksPageVisible(this.configureWizardData.selectedVsanClusterType,
            hasDiskGroupsOnWitness, isWitnessDeployedFromOvf, isVsanMaxWorkflow);
   };
}
