import {Component, OnInit} from "@angular/core";
import {HciClusterService} from "@generated/hci-cluster-service";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {ExistingDvsData} from "@generated/existing-dvs-data";

@Component({
   templateUrl: "existing-dvs-selector-dialog.html",
   selector: "existing-dvs-selector-dialog",
   styleUrls: ["existing-dvs-selector-dialog.scss"],
})
export class ExistingDvsSelectorDialogComponent implements OnInit {

   static readonly DVS_GRID_PAGE_SIZE: number = 10;
   VsanUiUtils = VsanUiUtils;

   private clusterRef: ManagedObject;

   isLoading: boolean;

   existingDvsList: ExistingDvsData[];

   selectedDvs: ExistingDvsData;

   newDvsSelection: boolean;

   private selectedDvsNames: string[];

   private selectedExistingDvsName: string;

   constructor(private hciClusterService: HciClusterService) {
   }

   ngOnInit() {
      const context: any = VsanUiUtils.getModalContext();
      this.clusterRef = context.clusterRef;
      this.selectedExistingDvsName = context.selectedExistingDvsName;
      this.selectedDvsNames = context.selectedDvsNames;

      this.requestData();
   }

   get dvsGridPageSize() {
      return ExistingDvsSelectorDialogComponent.DVS_GRID_PAGE_SIZE;
   }

   get alertMessage(): string {
      // If the DVS is already selected in the dialog, an error should be shown
      if (!this.newDvsSelection && !!this.selectedDvs && (this.selectedDvs.name !== this.selectedExistingDvsName) &&
            this.selectedDvsNames && this.selectedDvsNames.some(dvsName => dvsName === this.selectedDvs.name)) {
         return VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.dvsConfig.existingNetworkSelectorDialog.errorMessage");
      }
      return null;
   }

   onOkClicked = () => {
      if (this.newDvsSelection) {
         VsanUiUtils.closeModalDialog(null);
      } else {
         VsanUiUtils.closeModalDialog(this.selectedDvs);
      }
   };

   private requestData = () => {
      this.isLoading = true;
      this.hciClusterService.getExistingDvs(this.clusterRef, this.selectedExistingDvsName)
            .then(this.onDataRetrieved)
            .catch(this.onRequestError);
   };

   private onDataRetrieved = (result: any) => {
      if (result) {
         this.existingDvsList = result;
         if (this.existingDvsList && this.existingDvsList.length > 0 && this.existingDvsList[0].isSelected) {
            this.selectedDvs = this.existingDvsList[0];
         }
      }

      this.isLoading = false;
   };

   private onRequestError = (result: any) => {
      this.isLoading = false;
   };

   get noDvsSelected(): boolean {
      return !!this.alertMessage || (!this.newDvsSelection && !this.selectedDvs);
   }
}