import {VsanUiUtils} from "@util/vsan-util";
import {VsanClusterType} from "@generated/vsan-cluster-type";

export class VsanRdmaUtil {

   static canEnableRdma(hostHardwareSupportRdma: boolean, isLargeScaleSupportEnabled: boolean,
         configType: VsanClusterType, isRdmaStretchedClusterSupported: boolean): boolean {
      return hostHardwareSupportRdma && this.isClusterTypeSupported(configType, isRdmaStretchedClusterSupported)
            && !isLargeScaleSupportEnabled;
   }

   static getRdmaDisabledMessage(hostHardwareSupportRdma: boolean, isLargeScaleSupportEnabled: boolean,
         configType: VsanClusterType, isRdmaStretchedClusterSupported: boolean): string {
      let errorMessage: string = !hostHardwareSupportRdma
            ? VsanUiUtils.getString("vsan.services.network.rdmaSupport.notSupportedByAnyHostsHardware")
            : "";

      if (!this.isClusterTypeSupported(configType, isRdmaStretchedClusterSupported)) {
         errorMessage = errorMessage.concat(" ",
               VsanUiUtils.getString("vsan.services.network.rdmaSupport.notSupportedStretchedCluster")).trim();
      }

      if (isLargeScaleSupportEnabled) {
         errorMessage = errorMessage.concat(" ",
               VsanUiUtils.getString("vsan.services.network.rdmaSupport.notSupportedLargeScaleCluster")).trim();
      }

      return errorMessage;
   }

   private static isClusterTypeSupported(configType: VsanClusterType,
         isRdmaStretchedClusterSupported: boolean): boolean {
      return !this.isStretchedOrTwoNodeCluster(configType) || isRdmaStretchedClusterSupported;
   }

   private static isStretchedOrTwoNodeCluster(configType: VsanClusterType): boolean {
      return configType === VsanClusterType.STRETCHED_CLUSTER
            || configType === VsanClusterType.TWO_HOST_VSAN_CLUSTER;
   }
}
