import {Component, OnInit} from "@angular/core";
import {HciClusterService} from "@generated/hci-cluster-service";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {ExistingDvpgData} from "@generated/existing-dvpg-data";

@Component({
   templateUrl: "existing-dvpg-selector-dialog.html",
   selector: "existing-dvpg-selector-dialog",
   styleUrls: ["existing-dvpg-selector-dialog.scss"],
})
export class ExistingDvpgSelectorDialogComponent implements OnInit {

   static readonly DVPG_GRID_PAGE_SIZE: number = 10;
   VsanUiUtils = VsanUiUtils;

   private dvsRef: ManagedObject;

   isLoading: boolean;

   existingDvpgList: ExistingDvpgData[];

   selectedDvpg: ExistingDvpgData;

   newDvpgSelection: boolean;

   private selectedExistingDvpgName: string;

   constructor(private hciClusterService: HciClusterService) {
   }

   ngOnInit() {
      const context: any = VsanUiUtils.getModalContext();
      this.dvsRef = context.dvsRef;
      this.selectedExistingDvpgName = context.selectedExistingDvpgName;
      this.requestData();
   }

   get dvpgGridPageSize() {
      return ExistingDvpgSelectorDialogComponent.DVPG_GRID_PAGE_SIZE;
   }

   onOkClicked() {
      if (this.newDvpgSelection) {
         VsanUiUtils.closeModalDialog(null);
      } else {
         VsanUiUtils.closeModalDialog(this.selectedDvpg);
      }
   }

   private requestData = (): void => {
      this.isLoading = true;
      this.hciClusterService.getExistingDvpg(this.dvsRef, this.selectedExistingDvpgName)
            .then(this.onDataRetrieved)
            .catch(this.onRequestError);
   };

   private onDataRetrieved = (result: any): void => {
      if (result) {
         this.existingDvpgList = result;
         if (this.existingDvpgList && this.existingDvpgList.length > 0 && this.existingDvpgList[0].isSelected) {
            this.selectedDvpg = this.existingDvpgList[0];
         }
      }

      this.isLoading = false;
   };

   private onRequestError = (result: any): void => {
      this.isLoading = false;
   };
}