import {Injectable} from "@angular/core";
import {FormArray, FormControl} from "@angular/forms";
import {VsanUiUtils} from "@util/vsan-util";

@Injectable()
export class DistributedSwitchesPageValidator {

   public static readonly DVS_TO_PG_ASSIGNMENT_VALIDATION: string = "dvsToPgAssignmentValidation";
   public static readonly DVS_TO_ADAPTER_ASSIGNMENT_VALIDATION: string = "dvsToAdapterAssignmentValidation";
   public static readonly DVS_LOCALLY_DUPLICATE_NAMES_VALIDATION: string = "dvsLocalDuplicateNamesValidation";
   public static readonly DVS_EXISTING_DUPLICATE_NAMES_VALIDATION: string = "dvsExistingDuplicateNamesValidation";
   public static readonly DVPG_LOCALLY_DUPLICATE_NAMES_VALIDATION: string = "dvpgLocalDuplicateNamesValidation";
   public static readonly DVPG_EXISTING_DUPLICATE_NAMES_VALIDATION: string = "dvpgExistingDuplicateNamesValidation";

   public invalidDvsNameControl = (control: FormControl): boolean => {
      return control.touched && (control.hasError("required")
            || control.hasError("maxlength")
            || this.hasDvsLocalDuplicateNameError(control)
            || this.hasDvsExistingDuplicateNameError(control));
   };

   public invalidDvpgNameControl = (control: FormControl): boolean => {
      return control.touched && (control.hasError("required")
            || control.hasError("maxlength")
            || control.hasError(DistributedSwitchesPageValidator.DVPG_LOCALLY_DUPLICATE_NAMES_VALIDATION)
            || control.hasError(DistributedSwitchesPageValidator.DVPG_EXISTING_DUPLICATE_NAMES_VALIDATION));
   };

   public getDvsNameControlErrors = (control: FormControl): string => {
      if (control.hasError("required")) {
         return VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.dvsConfig.distributedSwitchesSection.dvsNameRequiredError");
      }

      if (control.hasError("maxlength")) {
         return VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.dvsConfig.distributedSwitchesSection.dvsNameLengthError");
      }

      if (this.hasDvsLocalDuplicateNameError(control)) {
         return VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.dvsConfig.distributedSwitchesSection.locallyDuplicateNameError");
      }

      if (this.hasDvsExistingDuplicateNameError(control)) {
         return VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.dvsConfig.distributedSwitchesSection.existingDvsDuplicateNameError");
      }

      return null;
   };

   public getDvpgNameControlErrors = (control: FormControl): string => {
      if (control.hasError("required")) {
         return VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.dvsConfig.distributedSwitchesSection.dvpgNameRequiredError");
      }

      if (control.hasError("maxlength")) {
         return VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.dvsConfig.distributedSwitchesSection.dvpgNameLengthError");
      }

      if (control.hasError(DistributedSwitchesPageValidator.DVPG_LOCALLY_DUPLICATE_NAMES_VALIDATION)) {
         return VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.dvsConfig.distributedSwitchesSection.dvpgLocallyDuplicateNameError");
      }

      if (control.hasError(DistributedSwitchesPageValidator.DVPG_EXISTING_DUPLICATE_NAMES_VALIDATION)) {
         return VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.dvsConfig.distributedSwitchesSection.dvpgExistingDuplicateNameError");
      }

      return null;
   };

   public getDvsAssignmentErrors = (dvsFormControls: FormControl[]): string[] => {
      let errors: string[] = [];
      dvsFormControls.forEach((control) => {
         if (this.hasDvsToAdapterAssignmentError(control)) {
            errors.push(VsanUiUtils.getString(
                  "vsan.cluster.hci.configureHciCluster.wizard.dvsConfig.distributedSwitchesSection.assignmentError",
                  control.value));
         }
      });

     return errors;
   };

   private hasDvsToAdapterAssignmentError = (control: FormControl): boolean => {
      return control.hasError(DistributedSwitchesPageValidator.DVS_TO_ADAPTER_ASSIGNMENT_VALIDATION);
   };

   private hasDvsLocalDuplicateNameError = (control: FormControl): boolean => {
      return this.hasDuplicateNameError(control, DistributedSwitchesPageValidator.DVS_LOCALLY_DUPLICATE_NAMES_VALIDATION);
   };

   private hasDvsExistingDuplicateNameError = (control: FormControl): boolean => {
      return this.hasDuplicateNameError(control, DistributedSwitchesPageValidator.DVS_EXISTING_DUPLICATE_NAMES_VALIDATION);
   };

   private hasDuplicateNameError = (control: FormControl, expectedError: string): boolean => {
      let formArray: FormArray = control.parent.parent as FormArray;

      if (formArray.hasError(expectedError)) {
         let error: any = formArray.getError(expectedError);
         if (error.duplicateNames.indexOf(control.value) != -1) {
            return true;
         }
      }

      return false;
   };
}
