import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {AbstractControl, FormArray, FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {IpParserService} from "@service/ip-parser.service";
import {VsanUiUtils} from "@util/vsan-util";
import {PageControls} from "@component/wizard/flow-wizard.component";
import {NetworkingSettingsComponent} from "./networking-settings.component";
import {NetServiceConfig} from "@generated/net-service-config";
import {IpAddressesRequestSpec} from "@generated/ip-addresses-request-spec";
import {NetworkService} from "@generated/network-service";
import IpType = NetServiceConfig.IpType;
import {Icon} from "@util/icon";
import {ReactiveFormUtil} from "@util/reactive-form-util";
import {InventoryNode} from "@generated/inventory-node";

@Component({
   templateUrl: "ipv4-settings.html",
   selector: "vsan-ipv4-settings",
   styleUrls: ["ipv4-settings.scss"],
})
export class Ipv4Settings implements OnInit {

   readonly IpType: typeof IpType = IpType;
   readonly Icon: typeof Icon = Icon;

   private static readonly IPV4_CONTROL_NAME: string = "ipv4";
   private static readonly SUBNET_MASK_CONTROL_NAME: string = "subnetMask";
   private static readonly GATEWAY_CONTROL_NAME: string = "gateway";
   private static readonly NAME_CONTROL_NAME: string = "name";
   private static readonly DEFAULT_IPV4_IP_TYPE: IpType = IpType.DHCP;
   private static readonly IP_TYPE_CONTROL_PATH: string = ReactiveFormUtil.getFormControlPath(
         NetworkingSettingsComponent.IPV4_SETTINGS_CONTROL_NAME,
         NetworkingSettingsComponent.IP_TYPE_CONTROL_NAME);
   private static readonly HOST_SETTINGS_GROUP_PATH: string = ReactiveFormUtil.getFormControlPath(
         NetworkingSettingsComponent.IPV4_SETTINGS_CONTROL_NAME,
         NetworkingSettingsComponent.HOST_SETTINGS_CONTROL_NAME);
   private static readonly IPV4_VALIDATION: string = "validateIp";
   private static readonly DUPLICATE_IPS_VALIDATION: string = "validateDuplicateIps";
   private static readonly DIFFERENT_SUBNET_VALIDATION: string = "validateDifferentSubnet";
   private static readonly SUBNET_MASK_VALIDATION: string = "validateSubnetMask";
   private static readonly FIRST_ROW_INDEX: string = "0";

   @Input()
   private pageControls: PageControls;

   @Input()
   hosts: InventoryNode[];

   @Input()
   private form: FormGroup;

   @Input()
   showDefaultGateway: boolean;

   @Input()
   ipSettingsInfoMessage: string;

   @Output()
   private onFormChange: EventEmitter<boolean> = new EventEmitter<boolean>();

   @Output()
   private ipAddressesRequestSpec: IpAddressesRequestSpec;

   requestingData: boolean;

   constructor(private fb: FormBuilder,
         private ipService: IpParserService,
         private networkService: NetworkService) {
   }

   ngOnInit(): void {
      this.buildForm();
      this.registerFormControlChangeHandlers();
   }

   private buildForm = (): void => {
      const hostFormGroups: FormGroup[] = this.hosts.map((host) =>
         this.fb.group({
            [Ipv4Settings.NAME_CONTROL_NAME]:
                  [host.name, null],
            [Ipv4Settings.IPV4_CONTROL_NAME]:
                  [null, [Validators.required, this.validateIpv4]],
            [Ipv4Settings.SUBNET_MASK_CONTROL_NAME]:
                  [null, [Validators.required, this.validateSubnetMask]],
            [Ipv4Settings.GATEWAY_CONTROL_NAME]:
                  [null, [this.validateDefaultGateway]],
         }, {validator: this.validateDifferentSubnetError}));
      const hostFormArray: FormArray = this.fb.array(hostFormGroups, this.validateDuplicateIps);

      const ipv4Settings: FormGroup = this.fb.group({
         [NetworkingSettingsComponent.IP_TYPE_CONTROL_NAME]: [Ipv4Settings.DEFAULT_IPV4_IP_TYPE, [Validators.required]],
         [NetworkingSettingsComponent.HOST_SETTINGS_CONTROL_NAME]: hostFormArray,
      });

      this.form.addControl(NetworkingSettingsComponent.IPV4_SETTINGS_CONTROL_NAME, ipv4Settings);
      this.toggleHostSettingsAvailability(this.ipType.value);
   };

   registerFormControlChangeHandlers = (): void => {
      this.ipType.valueChanges.forEach(this.toggleHostSettingsAvailability);

      this.hostSettings.controls.forEach((formGroup: FormGroup) => {
         for (const controlName in formGroup.controls) {
            const control: FormControl = formGroup.controls[controlName] as FormControl;
            control.statusChanges.forEach(() => {
               this.pageControls.rebuildToc();
            });
         }
      });
   };

   private toggleHostSettingsAvailability = (ipType: IpType): void => {
      switch (ipType) {
         case IpType.DHCP:
            this.hostSettings.disable();
            break;
         case IpType.STATIC:
            this.hostSettings.enable();
            break;
      }

      this.onFormChange.emit(true);
   };

   /** Form controls getters */

   get ipv4Settings(): FormGroup {
      return this.form.get(NetworkingSettingsComponent.IPV4_SETTINGS_CONTROL_NAME) as FormGroup;
   }

   get ipType(): FormControl {
      return this.form.get(Ipv4Settings.IP_TYPE_CONTROL_PATH) as FormControl;
   }

   get hostSettings(): FormArray {
      return this.form.get(Ipv4Settings.HOST_SETTINGS_GROUP_PATH) as FormArray;
   }

   get dhcpOnIpv4(): boolean {
      return this.ipType.value === IpType.DHCP;
   }

   getIpv4Control = (idx: string): FormControl => {
      const controlPath: string = ReactiveFormUtil.getFormControlPath(
            Ipv4Settings.HOST_SETTINGS_GROUP_PATH,
            idx,
            Ipv4Settings.IPV4_CONTROL_NAME);
      return this.form.get(controlPath) as FormControl;
   };

   getSubnetMaskControl = (idx: string): FormControl => {
      const controlPath: string = ReactiveFormUtil.getFormControlPath(
            Ipv4Settings.HOST_SETTINGS_GROUP_PATH,
            idx,
            Ipv4Settings.SUBNET_MASK_CONTROL_NAME);
      return this.form.get(controlPath) as FormControl;
   };

   getGatewayControl = (idx: string): FormControl => {
      const controlPath: string = ReactiveFormUtil.getFormControlPath(
            Ipv4Settings.HOST_SETTINGS_GROUP_PATH,
            idx,
            Ipv4Settings.GATEWAY_CONTROL_NAME);
      return this.form.get(controlPath) as FormControl;
   };

   private hasDifferentSubnet = (hostControls: FormGroup): boolean => {
      const ipv4Control: AbstractControl = hostControls.get(Ipv4Settings.IPV4_CONTROL_NAME);
      const subnetMaskControl: AbstractControl = hostControls.get(Ipv4Settings.SUBNET_MASK_CONTROL_NAME);
      const gatewayControl: AbstractControl = hostControls.get(Ipv4Settings.GATEWAY_CONTROL_NAME);

      if (this.isRowFilled(ipv4Control, gatewayControl, subnetMaskControl)) {
         return !this.ipService.isSameIpv4Subnet(ipv4Control.value.trim(),
               gatewayControl.value.trim(), subnetMaskControl.value.trim());
      }

      return false;
   };

   invalid = (formControl: AbstractControl): boolean => {
      return this.hostSettings.enabled && formControl.invalid && formControl.touched;
   };

   private hasSubnetMismatch = (formControl: AbstractControl): boolean => {
      return formControl.parent.hasError(Ipv4Settings.DIFFERENT_SUBNET_VALIDATION);
   };

   invalidIpv4Control = (idx: string): boolean => {
      const control: FormControl = this.getIpv4Control(idx);
      return this.invalid(control) || this.hasSubnetMismatch(control) || this.hasDuplicateIp(control);
   };

   invalidSubnetMaskControl = (idx: string): boolean => {
      const control: FormControl = this.getSubnetMaskControl(idx);
      return this.invalid(control) || this.hasSubnetMismatch(control);
   };

   invalidGatewayControl = (idx: string): boolean => {
      const control: FormControl = this.getGatewayControl(idx);
      return this.invalid(control) || this.hasSubnetMismatch(control);
   };

   validAutofillControl = (idx: string): boolean => {
      const ipv4: FormControl = this.getIpv4Control(idx);
      const subnet: FormControl = this.getSubnetMaskControl(idx);
      const gateway: FormControl = this.getGatewayControl(idx);
      if (this.isRowFilled(ipv4, gateway, subnet)) {
         return this.ipService.isSameIpv4Subnet(ipv4.value.trim(), gateway.value.trim(), subnet.value.trim());
      } else if (!gateway.value) {
         return ipv4.dirty && subnet.dirty && ipv4.value && subnet.value && ipv4.valid && subnet.valid;
      }

      return false;
   };

   private hasDuplicateIp = (formControl: AbstractControl): boolean => {
      const hostSettings: FormArray = formControl.parent.parent as FormArray;
      hostSettings.updateValueAndValidity();
      if (hostSettings.hasError(Ipv4Settings.DUPLICATE_IPS_VALIDATION)) {
         const duplicateIps: string[] =
               hostSettings.getError(Ipv4Settings.DUPLICATE_IPS_VALIDATION).duplicateIps;
         if (duplicateIps.indexOf(formControl.value) !== -1) {
            return true;
         }
      }

      return false;
   };

   private getDuplicateIps = (hostSettings: FormArray): string[] => {
      const formGroups: FormGroup[] = hostSettings.controls as FormGroup[];

      const ips: any = {};
      const result: string[] = [];
      formGroups.forEach((formGroup: FormGroup) => {
         const ipv4: FormControl = formGroup.get(Ipv4Settings.IPV4_CONTROL_NAME) as FormControl;
         const ip: string = ipv4.value;
         if (!ipv4.invalid && ipv4.touched && ipv4.dirty) {
            if (ips.hasOwnProperty(ip)) {
               result.push(ip);
            } else {
               ips[ip] = true;
            }
         }
      });

      return result;
   };

   getIpv4ControlErrors = (idx: string): string[] => {
      const formControl: FormControl = this.getIpv4Control(idx);

      const result: string[] = [];
      this.evaluateIpv4Validity(formControl, result);
      this.evaluateDuplicateIpv4Validity(formControl, result);
      const gatewayControl: FormControl = this.getGatewayControl(idx);
      if (gatewayControl.value) {
         this.evaluateDifferentSubnetValidity(formControl, result);
      }

      return result;
   };

   getSubnetMaskControlErrors = (idx: string): string[] => {
      const formControl: FormControl = this.getSubnetMaskControl(idx);

      const result: string[] = [];
      this.evaluateSubnetMaskValidity(formControl, result);
      const gatewayControl: FormControl = this.getGatewayControl(idx);
      if (gatewayControl.value) {
         this.evaluateDifferentSubnetValidity(formControl, result);
      }

      return result;
   };

   getGatewayControlErrors = (idx: string): string[] => {
      const formControl: FormControl = this.getGatewayControl(idx);

      const result: string[] = [];
      if (formControl.value) {
         this.evaluateGatewayValidity(formControl, result);
         this.evaluateDifferentSubnetValidity(formControl, result);
      }

      return result;
   };

   autoFillAddresses = (): void => {
      this.requestingData = true;
      const ipAddress: FormControl = this.getIpv4Control(Ipv4Settings.FIRST_ROW_INDEX);
      const subnetMask: FormControl = this.getSubnetMaskControl(Ipv4Settings.FIRST_ROW_INDEX);

      const ipv4RequestSpec: IpAddressesRequestSpec = IpAddressesRequestSpec.Factory.create(
            ipAddress.value.trim(), subnetMask.value.trim(), this.hostSettings.controls.length);

      this.networkService.getConsequentHostAddresses(ipv4RequestSpec)
            .then(this.fillSubsequentAddresses)
            .catch(() => this.requestingData = false);
   };

   private evaluateIpv4Validity = (formControl: FormControl, errors: string[]): void => {
      if (formControl.hasError(Ipv4Settings.IPV4_VALIDATION)) {
         errors.push(
               VsanUiUtils.getString(
                     "vsan.cluster.hci.configureHciCluster.wizard.networking.ipv4Tab.ipv4.wrongInput"));
      }
   };

   private evaluateSubnetMaskValidity = (formControl: FormControl, errors: string[]): void => {
      if (formControl.hasError(Ipv4Settings.SUBNET_MASK_VALIDATION)) {
         errors.push(
               VsanUiUtils.getString(
                     "vsan.cluster.hci.configureHciCluster.wizard.networking.ipv4Tab.subnetMask.wrongInput"));
      }
   };

   private evaluateDifferentSubnetValidity = (formControl: FormControl, errors: string[]): void => {
      if (formControl.parent.hasError(Ipv4Settings.DIFFERENT_SUBNET_VALIDATION)) {
         errors.push(
               VsanUiUtils.getString(
                     "vsan.cluster.hci.configureHciCluster.wizard.networking.ipv4Tab.differentSubnetError"));
      }
   };

   private evaluateDuplicateIpv4Validity = (formControl: FormControl, errors: string[]): void => {
      if (this.hasDuplicateIp(formControl)) {
         errors.push(
               VsanUiUtils.getString(
                     "vsan.cluster.hci.configureHciCluster.wizard.networking.ipv4Tab.duplicateIpsError"));
      }
   };

   private evaluateGatewayValidity = (formControl: FormControl, errors: string[]): void => {
      if (formControl.hasError(Ipv4Settings.IPV4_VALIDATION)) {
         errors.push(
               VsanUiUtils.getString(
                     "vsan.cluster.hci.configureHciCluster.wizard.networking.ipv4Tab.gateway.wrongInput"));
      }
   };

   /** Form control validators */

   private validateIpv4 = (c: AbstractControl): { [key: string]: any } | null => {
      const ip: string = String(c.value).trim();
      return this.ipService.isIpv4AddressValid(ip) ? null : {
         [Ipv4Settings.IPV4_VALIDATION]: {
            valid: false
         }
      };
   };

   private validateDefaultGateway = (c: AbstractControl): { [key: string]: any } | null => {
      return (c && c.value) ? this.validateIpv4(c) : null;
   };

   private validateSubnetMask = (c: AbstractControl): { [key: string]: any } | null => {
      const subnetMask: string = String(c.value).trim();
      return this.ipService.isSubnetMaskValid(subnetMask) ? null : {
         [Ipv4Settings.SUBNET_MASK_VALIDATION]: {valid: false},
      };
   };

   private validateDuplicateIps = (ipv4FormArray: FormArray): { [key: string]: any } | null => {
      const duplicateIps: string[] = this.getDuplicateIps(ipv4FormArray);
      return duplicateIps.length === 0 ? null : {
         [Ipv4Settings.DUPLICATE_IPS_VALIDATION]: {
            valid: false,
            duplicateIps: duplicateIps
         }
      };
   };

   private validateDifferentSubnetError = (formGroup: any): { [key: string]: any } | null => {
      return !this.hasDifferentSubnet(formGroup) ? null : {
         [Ipv4Settings.DIFFERENT_SUBNET_VALIDATION]: {valid: false},
      };
   };

   private isRowFilled(ipv4Control: AbstractControl, gatewayControl: AbstractControl,
         subnetMaskControl: AbstractControl): boolean {
      return ipv4Control.dirty && gatewayControl.dirty && subnetMaskControl.dirty &&
            ipv4Control.value && gatewayControl.value && subnetMaskControl.value &&
            ipv4Control.valid && gatewayControl.valid && subnetMaskControl.valid;
   }

   private fillSubsequentAddresses = (result: string[]): void => {
      const defaultGateway: FormControl = this.getGatewayControl(Ipv4Settings.FIRST_ROW_INDEX);
      const subnetMask: FormControl = this.getSubnetMaskControl(Ipv4Settings.FIRST_ROW_INDEX);

      for (let index: number = 1; index < this.hostSettings.controls.length; index++) {
         const formGroup: FormGroup = this.hostSettings.controls[index] as FormGroup;
         for (const controlName in formGroup.controls) {
            const control: FormControl = formGroup.controls[controlName] as FormControl;
            switch (controlName) {
               case Ipv4Settings.IPV4_CONTROL_NAME:
                  control.setValue(result[index - 1]);
                  break;
               case Ipv4Settings.SUBNET_MASK_CONTROL_NAME:
                  control.setValue(subnetMask.value);
                  break;
               case Ipv4Settings.GATEWAY_CONTROL_NAME:
                  control.setValue(defaultGateway.value);
                  break;
            }
         }
      }

      this.requestingData = false;
      this.pageControls.rebuildToc();
   };
}