import {Service} from '@generated/service';
import {HostAdapter} from '@generated/host-adapter';

export interface DvsSpec {

   name: string;
   services: Service[];
   adapters: HostAdapter[];
   existingDvsMor: any;
} //class DvsSpec

export module DvsSpec{
export class Factory {

   static create = (name: string, services: Service[], adapters: HostAdapter[], existingDvsMor: any): DvsSpec => {
      return { name: name, services: services, adapters: adapters, existingDvsMor: existingDvsMor, };
   }


} //class Factory

} //module DvsSpec
