import {Component, Input, OnInit} from "@angular/core";
import {AbstractControl, FormArray, FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {ReactiveFormUtil} from "@util/reactive-form-util";
import {PageControls} from "@component/wizard/flow-wizard.component";
import {NetServiceConfig} from "@generated/net-service-config";
import {Service} from "@generated/service";
import {DvpgData} from "@generated/dvpg-data";
import {VlanType} from "@generated/vlan-type";
import HostIpv4Config = NetServiceConfig.HostIpv4Config;
import HostIpv6Config = NetServiceConfig.HostIpv6Config;
import Protocol = NetServiceConfig.Protocol;
import IpType = NetServiceConfig.IpType;
import {InventoryNode} from "@generated/inventory-node";

@Component({
   templateUrl: "networking-settings.html",
   selector: "vsan-networking-settings",
   styleUrls: ["networking-settings.scss"],
})
export class NetworkingSettingsComponent implements OnInit {

   readonly Protocol: typeof Protocol = Protocol;

   static readonly IPV4_SETTINGS_CONTROL_NAME: string = "ipv4Settings";
   static readonly IPV6_SETTINGS_CONTROL_NAME: string = "ipv6Settings";
   static readonly IP_TYPE_CONTROL_NAME: string = "ipType";
   static readonly HOST_SETTINGS_CONTROL_NAME: string = "hostSettings";
   private static readonly DEFAULT_USE_VLAN: boolean = true;
   private static readonly DEFAULT_VLAN: number = 0;
   private static readonly MIN_VLAN: number = 0;
   private static readonly MAX_VLAN: number = 4095;
   private static readonly DEFAULT_PROTOCOL: Protocol = Protocol.IPV4;
   private static readonly USE_VLAN_CONTROL_NAME: string = "useVlan";
   private static readonly VLAN_CONTROL_NAME: string = "vlan";
   private static readonly PROTOCOL_CONTROL_NAME: string = "protocol";
   private static readonly IPV4_CONTROL_NAME: string = "ipv4";
   private static readonly SUBNET_MASK_CONTROL_NAME: string = "subnetMask";
   private static readonly GATEWAY_CONTROL_NAME: string = "gateway";
   private static readonly IPV6_CONTROL_NAME: string = "ipv6";
   private static readonly PREFIX_CONTROL_NAME: string = "prefix";

   @Input()
   pageControls: PageControls;

   @Input()
   hosts: InventoryNode[] = [];

   @Input()
   dsName: string;

   @Input()
   dvpg: DvpgData;

   @Input()
   isExtend: boolean;

   @Input()
   vlanId: string;

   @Input()
   vlanType: VlanType;

   @Input()
   service: Service;

   @Input()
   isDefaultGatewaySupported: boolean;

   form: FormGroup;
   showDefaultGateway: boolean;
   ipSettingsInfoMessage: string;

   constructor(private fb: FormBuilder) {}

   ngOnInit(): void {
      this.buildForm();
      this.registerFormControlChangeHandlers();

      this.ipSettingsInfoMessage =
          VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.networking.hostListDescription");
      if (this.service !== Service.VSAN || this.isDefaultGatewaySupported) {
          this.showDefaultGateway = true;
          this.ipSettingsInfoMessage = this.ipSettingsInfoMessage.concat(" ", VsanUiUtils.getString(
                "vsan.cluster.hci.configureHciCluster.wizard.networking.DefaultGatewayDescription"));
      }
   }

   private buildForm = (): void => {
      this.form = this.fb.group({
         useVlan: [NetworkingSettingsComponent.DEFAULT_USE_VLAN, [Validators.required]],
         vlan: [{value: NetworkingSettingsComponent.DEFAULT_VLAN, disabled: false},
                [Validators.required, this.validateVlan]],
         protocol: [NetworkingSettingsComponent.DEFAULT_PROTOCOL, [Validators.required]],
      });
   };

   private registerFormControlChangeHandlers = (): void => {
      if (!this.isExtend) {
         this.useVlan.valueChanges.forEach(this.disableSubsequentPages);
         this.vlan.valueChanges.forEach(this.disableSubsequentPages);
      }

      this.protocol.valueChanges.forEach((protocol: string) => this.toggleProtocol(Protocol[protocol]));
   };

   disableSubsequentPages = (): void => {
      this.form.updateValueAndValidity();
      this.pageControls.rebuildToc();
   };

   private toggleProtocol = (protocol: Protocol) => {
      this.toggleIpSettingsAvailability(this.ipv4Settings, this.isIPv4Compatible(protocol));
      this.toggleIpSettingsAvailability(this.ipv6Settings, this.isIPv6Compatible(protocol));
      this.disableSubsequentPages();
   };

   private toggleIpSettingsAvailability = (ipSettings: FormGroup, enable: boolean): void => {
      if (!ipSettings) {
         return;
      }

      if (enable) {
         const ipType: FormControl = ipSettings.get(NetworkingSettingsComponent.IP_TYPE_CONTROL_NAME) as FormControl;
         if (ipType.value === IpType.DHCP) {
            ipType.enable();
         } else {
            ipSettings.enable();
         }
      } else {
         ipSettings.disable();
      }
   };

   /** Form controls getters */

   get useVlan(): FormControl {
      return this.form.get(NetworkingSettingsComponent.USE_VLAN_CONTROL_NAME) as FormControl;
   }

   get vlan(): FormControl {
      return this.form.get(NetworkingSettingsComponent.VLAN_CONTROL_NAME) as FormControl;
   }

   get vlanLabel(): string {
      switch (this.vlanType) {
         case VlanType.VLAN_ID:
            return VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.networking.vlanIdLabel");
         case VlanType.VLAN_TRUNK:
            return VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.networking.vlanTrunkLabel");
         case VlanType.PVLAN:
            return VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.networking.pVlanIdLabel");
         default: return VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.networking.vlanIdLabel");
      }
   }

   get protocol(): FormControl {
      return this.form.get(NetworkingSettingsComponent.PROTOCOL_CONTROL_NAME) as FormControl;
   }

   get ipv4Settings(): FormGroup {
      return this.form.get(NetworkingSettingsComponent.IPV4_SETTINGS_CONTROL_NAME) as FormGroup;
   }

   get ipv6Settings(): FormGroup {
      return this.form.get(NetworkingSettingsComponent.IPV6_SETTINGS_CONTROL_NAME) as FormGroup;
   }

   get showIpv4Tab(): boolean {
      return this.isIPv4Compatible(this.protocol.value);
   }

   get showIpv6Tab(): boolean {
      return this.isIPv6Compatible(this.protocol.value);
   }

   private isIPv4Compatible(protocol: Protocol): boolean {
      return protocol === Protocol.IPV4 || protocol === Protocol.MIXED;
   }

   private isIPv6Compatible(protocol: Protocol): boolean {
      return protocol === Protocol.IPV6 || protocol === Protocol.MIXED;
   }

   get ipv4TabError(): boolean {
      const ipv4Settings: FormGroup =
            this.form.get(NetworkingSettingsComponent.IPV4_SETTINGS_CONTROL_NAME) as FormGroup;

      return this.isIpSettingsFormValid(ipv4Settings, true);
   }

   get ipv6TabError(): boolean {
      const ipv6Settings: FormGroup =
            this.form.get(NetworkingSettingsComponent.IPV6_SETTINGS_CONTROL_NAME) as FormGroup;

      return this.isIpSettingsFormValid(ipv6Settings, false);
   }

   disableVlanInput = (): void => {
      if (this.useVlan.value === false) {
         this.vlan.disable();
      } else {
         this.vlan.enable();
      }
   };

   invalid = (formControl: AbstractControl): boolean => {
      return formControl.invalid && formControl.dirty;
   };

   validate(): string[] {
      // validate only the enabled form fields
      if (this.useVlan && this.useVlan.value === true) {
         ReactiveFormUtil.validateControls([this.vlan]);
      }
      if (this.ipv4Settings && this.ipv4Settings.enabled) {
         ReactiveFormUtil.validateAllFormFields(this.ipv4Settings);
      }
      if (this.ipv6Settings && this.ipv6Settings.enabled) {
         ReactiveFormUtil.validateAllFormFields(this.ipv6Settings);
      }

      if (this.form.invalid) {
         return [
            VsanUiUtils.getString(
                  "vsan.cluster.hci.configureHciCluster.wizard.networking.notAcceptableErrors"),
         ];
      }

      return null;
   }

   getNetConfig = (service: Service): NetServiceConfig => {
      const formValue: any = this.form.value;
      let ipv4IpType: IpType = null;
      let hostIpv4Configs: HostIpv4Config[] = [];
      let ipv6IpType: IpType = null;
      let hostIpv6Configs: HostIpv6Config[] = [];
      if (this.isIPv4Compatible(formValue.protocol)) {
         ipv4IpType = formValue.ipv4Settings.ipType;
         if (ipv4IpType === IpType.STATIC) {
            hostIpv4Configs =
                  formValue.ipv4Settings.hostSettings.map((element) =>
                        HostIpv4Config.Factory.create(element.name, element.ipv4, element.subnetMask, element.gateway),
                  );
         }
      }

      if (this.isIPv6Compatible(formValue.protocol)) {
         ipv6IpType = formValue.ipv6Settings.ipType;
         if (ipv6IpType === IpType.STATIC) {
            hostIpv6Configs =
                  formValue.ipv6Settings.hostSettings.map((element) =>
                        HostIpv6Config.Factory.create(element.name, element.ipv6, +element.prefix, element.gateway),
                  );
         }
      }

      if (this.isExtend || this.dvpg.existingDvpg) {
         formValue.useVlan = false;
      }

      const dvpgName: string = this.dvpg ? this.dvpg.name : "";
      const existingDvpgMor: ManagedObject = this.dvpg ? this.dvpg.existingDvpg : null;

      return NetServiceConfig.Factory.create(
            service,
            formValue.useVlan,
            +formValue.vlan,
            dvpgName,
            existingDvpgMor,
            formValue.protocol,
            ipv4IpType,
            hostIpv4Configs,
            ipv6IpType ,
            hostIpv6Configs);
   };

   /** Form control validators */

   private validateVlan = (c: AbstractControl): {[key: string]: any} | null => {
      const vlan: number = Number(c.value);
      return vlan >= NetworkingSettingsComponent.MIN_VLAN && vlan <= NetworkingSettingsComponent.MAX_VLAN ? null : {
         validateVlan: {
            valid: false,
         },
      };
   };

   private isIpSettingsFormValid = (ipSettingsForm: FormGroup, isIpv4: boolean): boolean => {
      if (!ipSettingsForm || !ipSettingsForm.touched || ipSettingsForm.valid || !ipSettingsForm.controls) {
         return false;
      }

      const ipRows: FormArray = ipSettingsForm.controls[
            NetworkingSettingsComponent.HOST_SETTINGS_CONTROL_NAME] as FormArray;

      // display error if any of the rows is invalid and filled/touched
      return ipRows.controls.some((hostControl: FormGroup) => {
         return !hostControl.valid && (isIpv4 ? this.isIpv4ControlFilled(hostControl)
                     : this.isIpv6ControlFilled(hostControl));
      });
   };

   private isIpv4ControlFilled = (hostControl: FormGroup): boolean => {
      const ipv4Control: AbstractControl = hostControl.get(
            NetworkingSettingsComponent.IPV4_CONTROL_NAME);
      const subnetMaskControl: AbstractControl = hostControl.get(
            NetworkingSettingsComponent.SUBNET_MASK_CONTROL_NAME);
      const gatewayControl: AbstractControl = hostControl.get(
            NetworkingSettingsComponent.GATEWAY_CONTROL_NAME);

      return (ipv4Control.touched || ipv4Control.value)
            && (gatewayControl.touched || gatewayControl.value)
            && (subnetMaskControl.touched || subnetMaskControl.value);
   };

   private isIpv6ControlFilled = (hostControl: FormGroup): boolean => {
      const ipv6Control: AbstractControl = hostControl.get(
            NetworkingSettingsComponent.IPV6_CONTROL_NAME);
      const prefixControl: AbstractControl = hostControl.get(
            NetworkingSettingsComponent.PREFIX_CONTROL_NAME);
      const gatewayControl: AbstractControl = hostControl.get(
            NetworkingSettingsComponent.GATEWAY_CONTROL_NAME);

      return ipv6Control.touched && gatewayControl.touched && prefixControl.touched;
   };
}