import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {
   AbstractControl,
   FormArray,
   FormBuilder,
   FormControl,
   FormGroup,
   Validators,
} from "@angular/forms";
import {IpParserService} from "@service/ip-parser.service";
import {VsanUiUtils} from "@util/vsan-util";
import {PageControls} from "@component/wizard/flow-wizard.component";
import {NetworkingSettingsComponent} from "./networking-settings.component";
import {NetServiceConfig} from "@generated/net-service-config";
import IpType = NetServiceConfig.IpType;
import {ReactiveFormUtil} from "@util/reactive-form-util";
import {InventoryNode} from "@generated/inventory-node";
import {Icon} from "@util/icon";

@Component({
   templateUrl: "ipv6-settings.html",
   selector: "vsan-ipv6-settings",
   styleUrls: ["ipv6-settings.scss"],
})
export class Ipv6Settings implements OnInit {

   readonly IpType: typeof IpType = IpType;
   readonly Icon: typeof Icon = Icon;

   private static readonly NAME_CONTROL_NAME: string = "name";
   private static readonly IPV6_CONTROL_NAME: string = "ipv6";
   private static readonly PREFIX_CONTROL_NAME: string = "prefix";
   private static readonly GATEWAY_CONTROL_NAME: string = "gateway";
   private static readonly DEFAULT_IPV6_IP_TYPE: IpType = IpType.DHCP;
   private static readonly IP_TYPE_CONTROL_PATH: string = ReactiveFormUtil.getFormControlPath(
         NetworkingSettingsComponent.IPV6_SETTINGS_CONTROL_NAME,
         NetworkingSettingsComponent.IP_TYPE_CONTROL_NAME);
   private static readonly HOST_SETTINGS_GROUP_PATH: string = ReactiveFormUtil.getFormControlPath(
         NetworkingSettingsComponent.IPV6_SETTINGS_CONTROL_NAME,
         NetworkingSettingsComponent.HOST_SETTINGS_CONTROL_NAME);
   private static readonly IPV6_VALIDATION: string = "validateIp";
   private static readonly LINK_LOCAL_VALIDATION: string = "validateLikLocalAddress";
   private static readonly PREFIX_VALIDATION: string = "validatePrefix";
   private static readonly DUPLICATE_IPS_VALIDATION: string = "validateDuplicateIps";
   private static readonly DIFFERENT_SUBNET_VALIDATION: string = "validateDifferentSubnet";

   @Input()
   private pageControls: PageControls;

   @Input()
   private hosts: InventoryNode[];

   @Input()
   private form: FormGroup;

   @Input()
   showDefaultGateway: boolean;

   @Input()
   ipSettingsInfoMessage: string;

   @Output()
   private onFormChange: EventEmitter<boolean> = new EventEmitter<boolean>();

   constructor(private fb: FormBuilder, private ipService: IpParserService) {}

   ngOnInit(): void {
      this.buildForm();
      this.registerFormControlChangeHandlers();
   }

   private buildForm = (): void => {
      const hostFormGroups: any = this.hosts.map((host) => {
         return this.fb.group({
            [Ipv6Settings.NAME_CONTROL_NAME]: [host.name, null],
            [Ipv6Settings.IPV6_CONTROL_NAME]:
                  [null, [Validators.required, this.validateIpv6, this.validateLinkLocalAddress]],
            [Ipv6Settings.PREFIX_CONTROL_NAME]: [null, [Validators.required, this.validatePrefix]],
            [Ipv6Settings.GATEWAY_CONTROL_NAME]: [null, [this.validateIpv6DefaultGateway]],
         }, {validator: this.validateDifferentSubnetError});
      });

      const hostFormArray: any = this.fb.array(hostFormGroups, this.validateDuplicateIps);
      const ipv6Settings: any = this.fb.group({
         [NetworkingSettingsComponent.IP_TYPE_CONTROL_NAME]: [Ipv6Settings.DEFAULT_IPV6_IP_TYPE, [Validators.required]],
         [NetworkingSettingsComponent.HOST_SETTINGS_CONTROL_NAME]: hostFormArray
      });

      this.form.addControl(NetworkingSettingsComponent.IPV6_SETTINGS_CONTROL_NAME, ipv6Settings);
      this.toggleHostSettingsAvailability(this.ipType.value);
   };

   private registerFormControlChangeHandlers = (): void => {
      this.ipType.valueChanges.forEach(this.toggleHostSettingsAvailability);
      this.hostSettings.controls.forEach((formGroup: FormGroup) => {
         for (const controlName in formGroup.controls) {
            const control: FormControl = formGroup.controls[controlName] as FormControl;
            control.statusChanges.forEach(() => {
               this.pageControls.rebuildToc();
            });
         }
      });
   };

   private toggleHostSettingsAvailability = (ipType: IpType): void => {
      switch (ipType) {
         case IpType.DHCP:
         case IpType.ROUTER_ADVERTISEMENT:
            this.hostSettings.disable();
            break;
         case IpType.STATIC:
            this.hostSettings.enable();
            break;
      }

      this.onFormChange.emit(true);
   };

   /** Form controls getters */

   get ipv6Settings(): FormControl {
      return this.form.get(NetworkingSettingsComponent.IPV6_SETTINGS_CONTROL_NAME) as FormControl;
   }

   get ipType(): FormControl {
      return this.form.get(Ipv6Settings.IP_TYPE_CONTROL_PATH) as FormControl;
   }

   get hostSettings(): FormArray {
      return this.form.get(Ipv6Settings.HOST_SETTINGS_GROUP_PATH) as FormArray;
   }

   get disabledStaticIpv6(): boolean {
      return this.ipType.value !== IpType.STATIC;
   }

   getIpv6Control = (idx: string): FormControl => {
      const controlPath: string = ReactiveFormUtil.getFormControlPath(
            Ipv6Settings.HOST_SETTINGS_GROUP_PATH,
            idx,
            Ipv6Settings.IPV6_CONTROL_NAME);
      return this.form.get(controlPath) as FormControl;
   };

   getPrefixControl = (idx: string): FormControl => {
      const controlPath: string = ReactiveFormUtil.getFormControlPath(
            Ipv6Settings.HOST_SETTINGS_GROUP_PATH,
            idx,
            Ipv6Settings.PREFIX_CONTROL_NAME);
      return this.form.get(controlPath) as FormControl;
   };

   getGatewayControl = (idx: string): FormControl => {
      const controlPath: string = ReactiveFormUtil.getFormControlPath(
            Ipv6Settings.HOST_SETTINGS_GROUP_PATH,
            idx,
            Ipv6Settings.GATEWAY_CONTROL_NAME);
      return this.form.get(controlPath) as FormControl;
   };

   private hasDifferentSubnet = (hostControls: FormGroup): boolean => {
      const ipv6Control: AbstractControl = hostControls.get(Ipv6Settings.IPV6_CONTROL_NAME);
      const prefixControl: AbstractControl = hostControls.get(Ipv6Settings.PREFIX_CONTROL_NAME);
      const gatewayControl: AbstractControl = hostControls.get(Ipv6Settings.GATEWAY_CONTROL_NAME);

      if (ipv6Control.touched && gatewayControl.touched && prefixControl.touched &&
            ipv6Control.value && gatewayControl.value && prefixControl.value &&
            ipv6Control.valid && gatewayControl.valid && prefixControl.valid) {
         return !this.ipService.isSameIpv6Subnet(ipv6Control.value.trim(),
               gatewayControl.value.trim(), prefixControl.value.trim());
      }

      return false;
   };

   invalid = (formControl: AbstractControl): boolean => {
      return this.hostSettings.enabled && formControl.invalid && formControl.touched;
   };

   private hasSubnetMismatch = (formControl: AbstractControl): boolean => {
      return formControl.parent.hasError(Ipv6Settings.DIFFERENT_SUBNET_VALIDATION);
   };

   invalidIpv6Control = (idx: string): boolean => {
      const control: FormControl = this.getIpv6Control(idx);
      return this.invalid(control) || this.hasSubnetMismatch(control) || this.hasDuplicateIp(control);
   };

   invalidPrefixControl = (idx: string): boolean => {
      const control: FormControl = this.getPrefixControl(idx);
      return this.invalid(control) || this.hasSubnetMismatch(control);
   };

   invalidGatewayControl = (idx: string): boolean => {
      const control: FormControl = this.getGatewayControl(idx);
      return this.invalid(control) || this.hasSubnetMismatch(control);
   };

   private hasDuplicateIp = (formControl: AbstractControl): boolean => {
      const hostSettings: FormArray = formControl.parent.parent as FormArray;
      hostSettings.updateValueAndValidity();
      if (hostSettings.hasError(Ipv6Settings.DUPLICATE_IPS_VALIDATION)) {
         const duplicateIps: string[] =
               hostSettings.getError(Ipv6Settings.DUPLICATE_IPS_VALIDATION).duplicateIps;
         if (duplicateIps.indexOf(this.ipService.getNormalizedIpv6Address(formControl.value.trim())) !== -1) {
            return true;
         }
      }

      return false;
   };

   private getDuplicateIps = (hostSettings: FormArray): string[] => {
      const formGroups: FormGroup[] = hostSettings.controls as FormGroup[];

      const ips: any = {};
      const result: string[] = [];
      formGroups.forEach((formGroup: FormGroup) => {
         const ipv6: FormControl = formGroup.get(Ipv6Settings.IPV6_CONTROL_NAME) as FormControl;
         if (!ipv6.invalid && ipv6.touched && ipv6.dirty) {
            const ip: string = this.ipService.getNormalizedIpv6Address(ipv6.value.trim());
            if (ips.hasOwnProperty(ip)) {
               result.push(ip);
            } else {
               ips[ip] = true;
            }
         }
      });

      return result;
   };

   getIpv6ControlErrors = (idx: string): string[] => {
      const formControl: FormControl = this.getIpv6Control(idx);

      const result: string[] = [];
      this.evaluateIpv6Validity(formControl, result);
      this.evaluateDuplicateIpv6Validity(formControl, result);
      const gatewayControl: FormControl = this.getGatewayControl(idx);
      if (gatewayControl.value) {
         this.evaluateDifferentSubnetValidity(formControl, result);
      }
      this.evaluateLinkLocalValidity(formControl, result);

      return result;
   };

   getPrefixControlErrors = (idx: string): string[] => {
      const formControl: FormControl = this.getPrefixControl(idx);

      const result: string[] = [];
      this.evaluatePrefixValidity(formControl, result);
      const gatewayControl: FormControl = this.getGatewayControl(idx);
      if (gatewayControl.value) {
         this.evaluateDifferentSubnetValidity(formControl, result);
      }

      return result;
   };

   getGatewayControlErrors = (idx: string): string[] => {
      const formControl: FormControl = this.getGatewayControl(idx);

      const result: string[] = [];
      if (formControl.value) {
         this.evaluateGatewayValidity(formControl, result);
         this.evaluateDifferentSubnetValidity(formControl, result);
      }

      return result;
   };

   private evaluateIpv6Validity = (formControl: FormControl, errors: string[]): void => {
      if (formControl.hasError(Ipv6Settings.IPV6_VALIDATION)) {
         errors.push(VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.networking.ipv6Tab.ipv6.invalIpv6Address"));
      }
   };

   private evaluateLinkLocalValidity = (formControl: FormControl, errors: string[]): void => {
      if (formControl.hasError(Ipv6Settings.LINK_LOCAL_VALIDATION)) {
         errors.push(VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.networking.ipv6Tab.ipv6.linkLocalAddress"));
      }
   };

   private evaluatePrefixValidity = (formControl: FormControl, errors: string[]): void => {
      if (formControl.hasError(Ipv6Settings.PREFIX_VALIDATION)) {
         errors.push(VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.networking.ipv6Tab.prefix.wrongInput"));
      }
   };

   private evaluateDifferentSubnetValidity = (formControl: FormControl, errors: string[]): void => {
      if (formControl.parent.hasError(Ipv6Settings.DIFFERENT_SUBNET_VALIDATION)) {
         errors.push(VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.networking.ipv4Tab.differentSubnetError"));
      }
   };

   private evaluateDuplicateIpv6Validity = (formControl: FormControl, errors: string[]): void => {
      if (this.hasDuplicateIp(formControl)) {
         errors.push(VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.networking.ipv4Tab.duplicateIpsError"));
      }
   };

   private evaluateGatewayValidity = (formControl: FormControl, errors: string[]): void => {
      if (formControl.hasError(Ipv6Settings.IPV6_VALIDATION)) {
         errors.push(VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.networking.ipv4Tab.gateway.wrongInput"));
      }
   };

   /** Form control validators */

   private validateIpv6 = (ipv6Control: AbstractControl): {[key: string]: any} | null => {
      const ip: string = String(ipv6Control.value ? ipv6Control.value : "").trim();
      return this.ipService.isIpv6AddressValid(ip) ? null : {
         [Ipv6Settings.IPV6_VALIDATION]: { valid: false},
      };
   };

   private validateLinkLocalAddress = (ipv6Control: AbstractControl): {[key: string]: any} | null => {
      const ip: string = String(ipv6Control.value).trim();
      if (ipv6Control.parent) {
         const prefix: number = +(ipv6Control.parent.get(Ipv6Settings.PREFIX_CONTROL_NAME).value);
         return this.ipService.isLinkLocalIpv6Address(ip, prefix) ?
               {[Ipv6Settings.LINK_LOCAL_VALIDATION]: {valid: false}} : null;
      }
   };

   private validatePrefix = (prefixControl: AbstractControl): {[key: string]: any} | null => {
      const prefix: string = String(prefixControl.value).trim();
      return this.ipService.isSubnetPrefixValid(prefix) ? null :
            {[Ipv6Settings.PREFIX_VALIDATION]: { valid: false }};
   };

   private validateIpv6DefaultGateway = (gatewayControl: AbstractControl):
         {[key: string]: any} | null => {
      return (gatewayControl && gatewayControl.value) ?
            this.validateIpv6(gatewayControl) : null;
   };

   private validateDuplicateIps = (ipv6FormArray: FormArray): {[key: string]: any} | null => {
      const duplicateIps: string[] = this.getDuplicateIps(ipv6FormArray);
      return duplicateIps.length === 0 ? null : {
         [Ipv6Settings.DUPLICATE_IPS_VALIDATION]: {
            valid: false,
            duplicateIps: duplicateIps,
         },
      };
   };

   private validateDifferentSubnetError = (formGroup: any): {[key: string]: any} | null => {
      return !this.hasDifferentSubnet(formGroup) ? null : {
         [Ipv6Settings.DIFFERENT_SUBNET_VALIDATION]: {valid: false},
      };
   };
}