import {Component, OnInit} from "@angular/core";
import {AlertType} from "@component/validation/alert-type";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {PageItem} from "@component/wizard/page-item";
import {ClusterConfigData} from "@generated/cluster-config-data";
import {ConfigureClusterService} from "@generated/configure-cluster-service";
import {ConfigureWizardData} from "@generated/configure-wizard-data";
import {CsdService} from "@generated/csd-service";
import {DrsAutoLevel} from "@generated/drs-auto-level";
import {EncryptionPropertyProvider} from "@generated/encryption-property-provider";
import {EvcModeConfigData} from "@generated/evc-mode-config-data";
import {EvcModeValidationResult} from "@generated/evc-mode-validation-result";
import {HciClusterService} from "@generated/hci-cluster-service";
import {HostFaultDomainData} from "@generated/host-fault-domain-data";
import {KmipClusterData} from "@generated/kmip-cluster-data";
import {LockdownMode} from "@generated/lockdown-mode";
import {Service} from "@generated/service";
import {SpaceEfficiencyConfig} from "@generated/space-efficiency-config";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanClusterType} from "@generated/vsan-cluster-type";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {VsanRdmaService} from "@generated/vsan-rdma-service";
import {VumBaselineRecommendationType} from "@generated/vum-baseline-recommendation-type";
import {FaultDomainValidationService} from "@service/fault-domain-validation.service";
import {ManagedObject} from "@service/managed-object";
import {VsanDitUtil} from "@util/vsan-dit-util";
import {VsanRdmaUtil} from "@util/vsan-rdma-util";
import {VsanUiUtils} from "@util/vsan-util";

@WizardPage(
      VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.advancedOptionsPage.title"),
      VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.advancedOptionsPage.description"),
      [ConfigureClusterService, EncryptionPropertyProvider, CsdService],
)
@Component({
   templateUrl: "advanced-options-page.html",
   styleUrls: ["advanced-options-page.scss"],
})
export class AdvancedOptionsPageComponent implements OnInit, OnValidate {
   SpaceEfficiencyConfig: typeof SpaceEfficiencyConfig = SpaceEfficiencyConfig;

   AlertType: typeof AlertType = AlertType;

   @InputProperty()
   stretchClusterSupported: boolean;

   @InputProperty()
   hasSharedWitnessHost: boolean;

   @InputProperty()
   isClusterClientOrServer: boolean;

   @InputProperty()
   pageControls: PageControls;

   @InputProperty()
   kmipClusterData: KmipClusterData;

   @InputProperty()
   encryptionPermissions: boolean;

   @InputProperty()
   rekeyPermission: boolean;

   @InputProperty()
   private clusterHostFaultDomainData: HostFaultDomainData[];

   @InputProperty()
   @OutputProperty()
   clusterConfigData: ClusterConfigData;

   @InputProperty()
   @OutputProperty()
   configureWizardData: ConfigureWizardData;

   // All vSAN specific configuration should be recorded in this parameter
   @InputProperty()
   @OutputProperty()
   vsanConfigSpec: VsanConfigSpec;

   @InputProperty()
   isVsanMaxSupportedOnHosts: boolean;

   @InputProperty()
   @OutputProperty()
   evcModeConfigData: EvcModeConfigData;

   @InputProperty()
   clusterCapabilityData: VsanCapabilityData;

   @InputProperty()
   vcCapabilityData: VsanCapabilityData;

   @OutputProperty()
   isVsanMaxWorkflow: boolean;

   selectedHostFtt: string;
   allowCreateFaultDomains: boolean = true;
   showVsanMaxEnableOption: boolean = false;

   stackBlocksExpandedState: boolean[] = Array(5).fill(false);
   readonly HA_SETTINGS_INDEX: number = 0;
   readonly DRS_SETTINGS_INDEX: number = 1;
   readonly VSAN_SETTINGS_INDEX: number = 2;
   readonly HOST_SETTINGS_INDEX: number = 3;
   readonly EVM_SETTINGS_INDEX: number = 4;

   readonly AUTOMATION_OPTS: SelectorOption[] = [
      {
         id: DrsAutoLevel.MANUAL,
         label: VsanUiUtils.getString("vsan.cluster.configure.wizard.advancedOptions.auto.manual"),
      },
      {
         id: DrsAutoLevel.PARTIALLY_AUTOMATED,
         label: VsanUiUtils.getString("vsan.cluster.configure.wizard.advancedOptions.auto.partially"),
      },
      {
         id: DrsAutoLevel.FULLY_AUTOMATED,
         label: VsanUiUtils.getString("vsan.cluster.configure.wizard.advancedOptions.auto.fully"),
      }];

   static readonly DEFAULT_MIGRATION_THRESHOLD: number = 3;
   readonly AVAILABLE_MIGRATION_THRESHOLD: number[] = [1, 2, 3, 4, 5];

   private _clusterTypes: SelectorOption[];

   constructor(private hciClusterService: HciClusterService,
         private vsanRdmaService: VsanRdmaService,
         private faultDomainValidationService: FaultDomainValidationService) {
   }

   get availableVsanClusterTypes(): SelectorOption[] {
      if (this._clusterTypes) {
         return this._clusterTypes;
      }

      this._clusterTypes = [];
      this._clusterTypes.push({
         id: VsanClusterType.SINGLE_SITE_CLUSTER,
         label: VsanUiUtils.getString("vsan.cluster.configure.wizard.configurationType.singleSite.label"),
      });

      if (this.clusterConfigData.basicConfig.totalHosts === 2) {
         this._clusterTypes.push({
            id: VsanClusterType.TWO_HOST_VSAN_CLUSTER,
            label: VsanUiUtils.getString("vsan.cluster.configure.wizard.configurationType.twoHosts.label"),
         });
      }

      if (this.stretchClusterSupported && this.clusterConfigData.basicConfig.totalHosts > 1) {
         this._clusterTypes.push({
            id: VsanClusterType.STRETCHED_CLUSTER,
            label: VsanUiUtils.getString("vsan.cluster.configure.wizard.configurationType.stretchCluster.label"),
         });
      }

      return this._clusterTypes;
   }

   readonly VUM_BASELINE_PREFERENCE_TYPES: SelectorOption[] = [
      {
         id: VumBaselineRecommendationType.latestRelease,
         label: VsanUiUtils.getString("vsan.vum.baseline.recommendation.option.latest.release.label"),
      },
      {
         id: VumBaselineRecommendationType.latestPatch,
         label: VsanUiUtils.getString("vsan.vum.baseline.recommendation.option.latest.patch.label"),
      },
      {
         id: VumBaselineRecommendationType.noRecommendation,
         label: VsanUiUtils.getString("vsan.vum.baseline.recommendation.option.no.recommendation.label"),
      },
   ];

   readonly AVAILABLE_LOCKDOWN_MODES: SelectorOption[] = [
      {
         id: LockdownMode.DISABLED,
         label: VsanUiUtils.getString(
               "vsan.cluster.configure.wizard.advancedOptions.host.options.lockdown.mode.disabled"),
      },
      {
         id: LockdownMode.NORMAL,
         label: VsanUiUtils.getString(
               "vsan.cluster.configure.wizard.advancedOptions.host.options.lockdown.mode.normal"),
      },
      {
         id: LockdownMode.STRICT,
         label: VsanUiUtils.getString(
               "vsan.cluster.configure.wizard.advancedOptions.host.options.lockdown.mode.strict"),
      },
   ];

   availableHostFtt: number[] = [];
   hostHardwareSupportRdma: boolean = false;

   async ngOnInit(): Promise<void> {
      if (this.evcModeConfigData.supportedIntelEvcMode
            && this.evcModeConfigData.supportedIntelEvcMode.length > 0) {
         this.clusterConfigData.selectedEvcMode = this.evcModeConfigData.supportedIntelEvcMode[0].id;
      } else if (this.evcModeConfigData.supportedAmdEvcMode
            && this.evcModeConfigData.supportedAmdEvcMode.length > 0) {
         this.clusterConfigData.selectedEvcMode = this.evcModeConfigData.supportedAmdEvcMode[0].id;
      }

      if (!this.clusterConfigData.migrationThreshold) {
         this.clusterConfigData.migrationThreshold = AdvancedOptionsPageComponent.DEFAULT_MIGRATION_THRESHOLD;
      }

      if (this.clusterConfigData.basicConfig.totalHosts < 2) {
         // In case the cluster doesn't have a host, set the available ftt=1
         this.availableHostFtt.push(1);
      } else {
         // Start from 1 because the minimum FTT is 1
         for (let i: number = 1; i < this.clusterConfigData.basicConfig.totalHosts; i++) {
            this.availableHostFtt.push(i);
         }
      }

      if (this.isEncryptionSupported && this.kmipClusterData.availableKmipClusters &&
            this.kmipClusterData.availableKmipClusters.length) {
         this.vsanConfigSpec.kmipClusterId =
               this.vsanConfigSpec.kmipClusterId || this.kmipClusterData.availableKmipClusters[0];
      }

      // There is problem with setting numbers directly in select ngModel
      // this requires the value to be stored in a string property and
      // on change to be moved to the real mode object
      this.selectedHostFtt = "" + this.clusterConfigData.hostFTT;

      // Override the largeClusterSupportEnabled value from Configure wizard data
      // as there is a custom logic for it in HciClusterService.java
      this.vsanConfigSpec.advancedOptions.largeClusterSupportEnabled
            = this.configureWizardData.largeScaleClusterSupport;

      // Check if vSAN Max is already enabled on the cluster
      this.isVsanMaxWorkflow = this.vsanConfigSpec.isVsanMaxEnabled;

      if (this.clusterConfigData.basicConfig.vsanEnabled) {
         await this.getRdmaState();
      }

      if (this.isVsanMaxSupportedOnHosts
            && this.clusterConfigData.basicConfig.vsanEnabled
            && !this.clusterConfigData.basicConfig.isVsanMaxEnabled) {
         this.showVsanMaxEnableOption = true;
      }
      this.initStackBlocksExpandedState();
      this.validateSelection();
   }

   private initStackBlocksExpandedState(): void {
      if (this.showVsanMaxEnableOption) {
         // If we show the banner to enable vSAN MAX we should auto-expand the vSAN settings section,
         // where the setting is present.
         this.stackBlocksExpandedState[this.VSAN_SETTINGS_INDEX] = true;
      } else if (this.clusterConfigData.basicConfig.haEnabled) {
         this.stackBlocksExpandedState[this.HA_SETTINGS_INDEX] = true;
      } else if (this.clusterConfigData.basicConfig.drsEnabled) {
         this.stackBlocksExpandedState[this.DRS_SETTINGS_INDEX] = true;
      } else if (this.clusterConfigData.basicConfig.vsanEnabled) {
         this.stackBlocksExpandedState[this.VSAN_SETTINGS_INDEX] = true;
      } else {
         this.stackBlocksExpandedState[this.HOST_SETTINGS_INDEX] = true;
      }
   }

   toggleStackBlockExpandedState(stackBlockIndex: number): boolean {
      const newIndexState: boolean = !this.stackBlocksExpandedState[stackBlockIndex];
      this.stackBlocksExpandedState.forEach((value, index) => {
         this.stackBlocksExpandedState[index] = index === stackBlockIndex ? newIndexState : false;
      });
      return newIndexState;
   }

   get isEncryptionSupported(): boolean {
      return this.clusterCapabilityData?.isEncryptionSupported &&
            this.encryptionPermissions;
   }

   get isDataInTransitEncryptionSupported(): boolean {
      return this.clusterCapabilityData?.isDataInTransitEncryptionSupported && this.rekeyPermission;
   }

   get isSpaceEfficiencySupported(): boolean {
      return this.clusterCapabilityData?.isDeduplicationAndCompressionSupported
            || this.clusterCapabilityData?.isCompressionOnlySupported;
   }

   onDataInTransitEncryptionChange(): void {
      if (this.vsanConfigSpec.enableDataInTransitEncryption) {
         this.vsanConfigSpec.rekeyInterval = VsanDitUtil.DEFAULT_DIT_REKEY_INTERVAL;
      }
   }

   onHostFttChange(): void {
      this.clusterConfigData.hostFTT = +this.selectedHostFtt;
   }

   validateAdvancedSettings(): string[] {
      const validationErrors: string[] = [];
      if (!this.clusterConfigData.basicConfig.vsanEnabled) {
         return validationErrors;
      }

      if (this.isEncryptionSupported && this.vsanConfigSpec.enableDataAtRestEncryption
            && !this.vsanConfigSpec.kmipClusterId) {
         validationErrors.push(VsanUiUtils.getString("vsan.generalConfig.kmsNotSelected.warning"));
      }

      switch (this.configureWizardData.selectedVsanClusterType) {
         case VsanClusterType.SINGLE_SITE_CLUSTER:
            if (this.allowCreateFaultDomains) {
               validationErrors.push(...this.faultDomainValidationService.getFaultDomainValidationErrors(this.clusterHostFaultDomainData));
            }
            break;
         case VsanClusterType.TWO_HOST_VSAN_CLUSTER:
            if (this.clusterConfigData.basicConfig.totalHosts !== 2) {
               validationErrors.push(
                     VsanUiUtils.getString("vsan.generalConfig.wrongHostNumberForRobo",
                           this.clusterConfigData.basicConfig.totalHosts));
            }
            validationErrors.push(...this.faultDomainValidationService.getFaultDomainValidationErrors(this.clusterHostFaultDomainData));
            break;
         case VsanClusterType.STRETCHED_CLUSTER:
            if (!this.stretchClusterSupported) {
               validationErrors.push(VsanUiUtils.getString("vsan.generalConfig.stretchedClusterNotSupported"));
            } else if (this.clusterConfigData.basicConfig.totalHosts < 2) {
               validationErrors.push(VsanUiUtils.getString("vsan.generalConfig.notEnoughHostsForStretchedCluster"));
               validationErrors.push(...this.faultDomainValidationService.getFaultDomainValidationErrors(this.clusterHostFaultDomainData));
            }
            break;
      }
      return validationErrors;
   }

   validate(): string[] | Promise<string[]> {
      const advancedOptionsValidation: string[] = this.validateAdvancedSettings();
      return advancedOptionsValidation.length > 0 ? advancedOptionsValidation : this.validateEvcMode();
   }

   vsanClusterTypeChanged(): void {
      switch (this.configureWizardData.selectedVsanClusterType) {
         case VsanClusterType.STRETCHED_CLUSTER:
            this.configureWizardData.showSingleSiteFaultDomainsPage = false;
            this.configureWizardData.enableFaultDomainForSingleSiteCluster = true;
            this.allowCreateFaultDomains = false;
            break;
         case VsanClusterType.TWO_HOST_VSAN_CLUSTER:
            this.configureWizardData.showSingleSiteFaultDomainsPage = false;
            this.configureWizardData.enableFaultDomainForSingleSiteCluster = false;
            this.allowCreateFaultDomains = false;
            break;
         case VsanClusterType.SINGLE_SITE_CLUSTER:
            this.configureWizardData.enableFaultDomainForSingleSiteCluster = false;
            this.configureWizardData.showSingleSiteFaultDomainsPage =
                  this.configureWizardData.enableFaultDomainForSingleSiteCluster;
            this.allowCreateFaultDomains = true;
            break;
      }

      this.updateRdmaState();

      this.validateSelection();
   }

   // Disable RDMA toggle when Stretched/2 node or large scale cluster is enabled
   updateRdmaState(): void {
      if (this.clusterCapabilityData?.isRdmaSupported && !this.canEnableRdma) {
         this.vsanConfigSpec.enableRdma = false;
      }
   }

   enableFaultDomainForSingleSiteClusterChanged(): void {
      this.configureWizardData.showSingleSiteFaultDomainsPage =
            this.configureWizardData.enableFaultDomainForSingleSiteCluster;
      this.validateSelection();
   }

   updateVsanMaxState(): void {
      this.clusterConfigData.basicConfig.isVsanMaxEnabled = this.isVsanMaxWorkflow;
      this.vsanConfigSpec.isVsanMaxEnabled = this.isVsanMaxWorkflow;
      if (this.isVsanMaxWorkflow) {
         this.vsanConfigSpec.spaceEfficiencyConfig = SpaceEfficiencyConfig.NONE;
      }
   }

   validateSelection(): void {
      let disableNextButton: boolean = false;
      this.clearPageState();

      const errorMessages: string[] = this.validateAdvancedSettings();
      if (errorMessages.length) {
         this.pageControls.addValidationErrors(errorMessages);
         disableNextButton = true;
      }
      (this.pageControls as PageItem).nextButtonIsDisabled = disableNextButton;
   }

   /**
    * There is a bug in angular2 on <select> element with *ngFor,
    * https://github.com/angular/angular/issues/4843
    * To workaround this, we have to parse the string to number manually
    */
   parseMigrationThreshold = (): void => {
      this.clusterConfigData.migrationThreshold = +this.clusterConfigData.migrationThreshold;
   };

   clearPageState = (): void => {
      this.rebuildToc();
      this.pageControls.clearValidationErrors();
   };

   // Triggered on changed value in the page
   rebuildToc(): void {
      this.pageControls.rebuildToc();
   }

   validateEvcMode = (): Promise<string[]> => {
      if (!this.clusterConfigData.enableEVC) {
         return null;
      }

      return this.hciClusterService.getEvcModeValidationResult(ManagedObject.contextObject,
            this.clusterConfigData.selectedEvcMode)
            .then((results: EvcModeValidationResult[]) => {
               if (results.length) {
                  return this.aggregateEvcModeValidationResults(results);
               }
            })
            .catch((error) => {
               return [error];
            });
   };

   /**
    * The evc validation results are seperated by the host.
    * This means the error messages could be the same for all the hosts,
    * we need to group them to avoid displaying duplicated messages.
    *
    * Another thing is, the host in the validation result is host id, something like 'host-15'.
    * This is not what we wanted, we need the IP address, and it can be found in the clusterHostFaultDomainData
    */
   private aggregateEvcModeValidationResults = (results: EvcModeValidationResult[]): string[] => {
      const errorMap: Map<string, string[]> = new Map();
      results.forEach((result: EvcModeValidationResult) => {
         const hosts: string[] = errorMap.get(result.errorMessage) || [];
         Array.prototype.push.apply(hosts, result.hostRefs
               .filter(host => hosts.indexOf(host.value) === -1)
               .map((host): string => {
                  return host.value;
               }));
         errorMap.set(result.errorMessage, hosts);
      });

      const errorMessages: string[] = [];
      errorMap.forEach((val: string[], key: string): void => {
         const hostNames: string[] = val.map((host): string => {
            return this.clusterHostFaultDomainData
                  .filter(domain => ManagedObject.fromUid(domain.hostUid).value === host)[0].name;
         });
         errorMessages.push(`${key} ${hostNames.join(", ")}`);
      });

      return errorMessages;
   };

   private async getRdmaState(): Promise<void> {
      // Due to some corner cases when RDMA is enabled when starting HCI configure wizard,
      // it should be explicitly disabled.
      this.vsanConfigSpec.enableRdma = false;
      await this.getIsRdmaHardwareSupported();
   }

   private async getIsRdmaHardwareSupported(): Promise<void> {
      this.pageControls.setBusyState();
      let vSanHostAdapters: string[] = [];

      // Check if there are configured dvsSpecs, because in the HCI wizard - the network settings could be skipped
      if (this.clusterConfigData.dvsSpecs) {
         vSanHostAdapters = this.clusterConfigData.dvsSpecs.find(
               dvs => dvs.services.includes(Service.VSAN)).adapters.map(adapter => adapter.deviceName);
      }

      try {
         this.hostHardwareSupportRdma = await this.vsanRdmaService.isRdmaHardwareSupported(
               ManagedObject.contextObject, vSanHostAdapters);
      } catch (error) {
         this.pageControls.addValidationErrors([error]);
      }

      this.pageControls.clearBusyState();
   }

   get canEnableRdma(): boolean {
      return VsanRdmaUtil.canEnableRdma(this.hostHardwareSupportRdma,
            this.vsanConfigSpec.advancedOptions.largeClusterSupportEnabled,
            this.configureWizardData.selectedVsanClusterType,
            this.clusterCapabilityData.isRdmaStretchedClusterSupported);
   }

   get rdmaDisabledMessage(): string {
      return VsanRdmaUtil.getRdmaDisabledMessage(this.hostHardwareSupportRdma,
            this.vsanConfigSpec.advancedOptions.largeClusterSupportEnabled,
            this.configureWizardData.selectedVsanClusterType,
            this.clusterCapabilityData.isRdmaStretchedClusterSupported);
   }

   get ditUnavailableMessage(): string {
      return VsanDitUtil.getDitUnavailableMessage(
            this.vsanConfigSpec,
            this.isClusterClientOrServer,
            this.hasSharedWitnessHost,
            this.clusterCapabilityData.isDitSharedWitnessInteroperabilitySupported);
   }
}

/**
 * The interface to represent the object type of a HTML select option
 */
interface SelectorOption {
   id: DrsAutoLevel | LockdownMode | VsanClusterType | VumBaselineRecommendationType;
   label: string;
}
