import {Component, OnInit, ViewChild} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {VsanUiUtils} from "@util/vsan-util";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {NetworkingSettingsComponent} from "../component/networking-settings.component";
import {ClusterConfigData} from "@generated/cluster-config-data";
import {Service} from "@generated/service";
import {ConfigureWizardData} from "@generated/configure-wizard-data";
import {DvpgData} from "@generated/dvpg-data";
import {VlanType} from "@generated/vlan-type";
import {HciClusterService} from "@generated/hci-cluster-service";
import {VlanData} from "@generated/vlan-data";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {InventoryNode} from "@generated/inventory-node";

@WizardPage(
      VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.vmotionTrafficPage.title"),
      VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.vmotionTrafficPage.description"),
)
@Component({
   templateUrl: "vmotion-traffic-page.html",
})
export class VmotionTrafficPageComponent implements OnInit, OnValidate {

   @ViewChild(NetworkingSettingsComponent)
   networkingSettings: NetworkingSettingsComponent;

   @InputProperty()
   pageControls: PageControls;

   @InputProperty("notConfiguredClusterHosts")
   hosts: InventoryNode[] = [];

   @InputProperty()
   clusterConfigData: ClusterConfigData;

   @InputProperty(InputProperty.required(false))
   vmotionDvsName: string;

   @InputProperty()
   configureWizardData: ConfigureWizardData;

   @InputProperty(InputProperty.required(false))
   vmotionDvpg: DvpgData;

   @InputProperty()
   clusterCapabilityData: VsanCapabilityData;

   currentService: Service = Service.VMOTION;

   vlan: string;

   vlanType: VlanType;

   constructor(private hciService: HciClusterService) {
   }

   @OutputProperty()
   vmotionTrafficForm: any;

   ngOnInit(): void {
      if (this.configureWizardData.isExtend) {
         this.vmotionDvsName =
               this.clusterConfigData.basicConfig.dvsDataByService[Service.VMOTION].dvsName;
         this.vlan =
               this.clusterConfigData.basicConfig.dvsDataByService[Service.VMOTION].vlan;
         this.vlanType =
               this.clusterConfigData.basicConfig.dvsDataByService[Service.VMOTION].vlanType;
      }

      if (this.vmotionDvpg && this.vmotionDvpg.existingDvpg) {
         this.hciService.getDvpgVlan(this.vmotionDvpg.existingDvpg).then((vlanData: VlanData) => {
            if (vlanData) {
               this.vlanType = vlanData.vlanType;
               this.vlan = vlanData.vlan;
            }
         });
      }
   }

   validate(): string[] {
      const result: string[] = this.networkingSettings.validate();
      if (!result) {
         // remove previous config
         if (this.clusterConfigData.netServiceConfigs) {
            this.clusterConfigData.netServiceConfigs =
                  this.clusterConfigData.netServiceConfigs.filter((config) => config.service !== Service.VMOTION);
         } else {
            this.clusterConfigData.netServiceConfigs = [];
         }

         // fill cluster config with vMotion configuration
         this.clusterConfigData.netServiceConfigs.push(this.networkingSettings.getNetConfig(Service.VMOTION));
      }

      return result;
   }
}
