import {Component} from "@angular/core";
import {AbstractControl, FormArray, FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {ClusterConfigData} from "@generated/cluster-config-data";
import {ConfigureWizardData} from "@generated/configure-wizard-data";
import {DvpgData} from "@generated/dvpg-data";
import {DvsSpec} from "@generated/dvs-spec";
import {ExistingDvpgData} from "@generated/existing-dvpg-data";
import {ExistingDvsData} from "@generated/existing-dvs-data";
import {HciClusterService} from "@generated/hci-cluster-service";
import {HostAdapter} from "@generated/host-adapter";
import {Service} from "@generated/service";
import {NavigationService} from "@navigation/navigation.service";
import {ManagedObject} from "@service/managed-object";
import {Logger} from "@util/logger";
import {ModalBuilder} from "@util/modal-builder";
import {ReactiveFormUtil} from "@util/reactive-form-util";
import {StringUtil} from "@util/string-util";
import {VsanUiUtils} from "@util/vsan-util";
import {DistributedSwitchesPageValidator} from "./distributed-switches-page.validator";

@WizardPage(
      VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.distributedSwitchesPage.title"),
      VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.distributedSwitchesPage.description"),
)
@Component({
   templateUrl: "distributed-switches-page.html",
   styleUrls: ["distributed-switches-page.scss"],
})
export class DistributedSwitchesPageComponent implements OnValidate {

   static readonly VVD_ROUTE: string = "/solutions/software-defined-datacenter/validated-designs.html";

   static readonly OPT_OUT_OF_NET_CONFIG_CONTROL_NAME: string = "optOutOfNetworking";

   static readonly NET_MAX_COUNT: number = 3;
   static readonly NET_CONFIG_FORM_GROUP: string = "netConfigSection";
   static readonly DVS_COUNT_CONTROL_NAME: string = "physicalNetworksCount";
   static readonly DVS_COUNT_CONTROL_PATH: string = ReactiveFormUtil.getFormControlPath(
         DistributedSwitchesPageComponent.NET_CONFIG_FORM_GROUP,
         DistributedSwitchesPageComponent.DVS_COUNT_CONTROL_NAME);
   static readonly DVS_COUNT_DEFAULT_VALUE: number = 1;

   static readonly DVS_FORM_ARRAY: string = "switchesSection";
   static readonly DVS_FORM_ARRAY_PATH: string = ReactiveFormUtil.getFormControlPath(
         DistributedSwitchesPageComponent.NET_CONFIG_FORM_GROUP,
         DistributedSwitchesPageComponent.DVS_FORM_ARRAY);
   static readonly DVS_CONTROL_NAME: string = "switch";

   static readonly PG_FORM_GROUP: string = "portGroupSection";
   static readonly PG_FORM_GROUP_PATH: string = ReactiveFormUtil.getFormControlPath(
         DistributedSwitchesPageComponent.NET_CONFIG_FORM_GROUP,
         DistributedSwitchesPageComponent.PG_FORM_GROUP);
   readonly VMOTION_PG_CONTROL_NAME: string = Service.VMOTION;
   readonly VSAN_PG_CONTROL_NAME: string = Service.VSAN;

   static readonly VMOTION_PG_NAME_CONTROL_NAME: string = "dvpgVmotionName";
   static readonly VSAN_PG_NAME_CONTROL_NAME: string = "dvpgVsanName";

   static readonly VMOTION_PG_NAME: string = "vMotion";
   static readonly VSAN_PG_NAME: string = "vSAN";

   static readonly PG_DEFAULT_SWITCH: string = "0";

   static readonly ADAPTERS_FORM_ARRAY: string = "adaptersSection";
   static readonly ADAPTERS_FORM_ARRAY_PATH: string = ReactiveFormUtil.getFormControlPath(
         DistributedSwitchesPageComponent.NET_CONFIG_FORM_GROUP,
         DistributedSwitchesPageComponent.ADAPTERS_FORM_ARRAY);
   static readonly ADAPTER_NOT_IN_USE: string = "notInUse";

   static readonly DVS_TO_ADAPTER_ASSIGNMENT_VALIDATION: string = "dvsToAdapterAssignmentValidation";
   static readonly DVS_LOCALLY_DUPLICATE_NAMES_VALIDATION: string = "dvsLocalDuplicateNamesValidation";
   static readonly DVS_EXISTING_DUPLICATE_NAMES_VALIDATION: string = "dvsExistingDuplicateNamesValidation";
   static readonly DVPG_LOCALLY_DUPLICATE_NAMES_VALIDATION: string = "dvpgLocalDuplicateNamesValidation";
   static readonly DVPG_EXISTING_DUPLICATE_NAMES_VALIDATION: string = "dvpgExistingDuplicateNamesValidation";
   private static readonly DVS_SERVICE_SEPARATOR: string = "-";
   private static readonly DVS_SERVICE_WORD_SEPARATOR: string = " ";

   private readonly HCI_WIZARD_ROUTE_PATH: string = "hciCluster_configureWizard/";
   private readonly EXISTING_DVS_SELECTOR_COMPONENT: string = "existingDvsSelector";
   private readonly EXISTING_DVPG_SELECTOR_COMPONENT: string = "existingDvpgSelector";

   @InputProperty()
   private clusterRef: ManagedObject;

   @InputProperty()
   private pageControls: PageControls;

   @InputProperty()
   @OutputProperty()
   clusterConfigData: ClusterConfigData;

   @InputProperty()
   private uniqueNewDvsNames: string[];

   @InputProperty()
   private existingDvsNames: string[];

   @InputProperty()
   private existingPgNames: string[];

   @InputProperty()
   physicalAdapters: HostAdapter[];

   @InputProperty()
   configureWizardData: ConfigureWizardData;

   @OutputProperty()
   private vmotionDvsName: string;

   @OutputProperty()
   private storageDvsName: string;

   @OutputProperty()
   vmotionDvpg: DvpgData;

   @OutputProperty()
   storageDvpg: DvpgData;

   distributedSwitchesForm: FormGroup;
   selectedExistingDvs: ExistingDvsData[] = [];
   existingVmotionDvpg: ExistingDvpgData;
   existingStorageDvpg: ExistingDvpgData;
   showExistingVmotionButton: boolean = false;
   showExistingVsanButton: boolean = false;
   dvsNames: string[] = [];

   constructor(
         private formBuilder: FormBuilder,
         public validator: DistributedSwitchesPageValidator,
         private hciClusterService: HciClusterService,
         private navigationService: NavigationService) {
   }

   ngOnInit(): void {
      this.buildForm();
      this.registerFormControlChangeHandlers();
   }

   private buildForm = (): void => {
      const dvsFormArray: FormArray =
            this.createDvsFormArray(DistributedSwitchesPageComponent.DVS_COUNT_DEFAULT_VALUE);

      const controlsConfig: any = {};

      if (this.clusterConfigData.basicConfig.drsEnabled) {
         controlsConfig[this.VMOTION_PG_CONTROL_NAME] =
               [DistributedSwitchesPageComponent.PG_DEFAULT_SWITCH, null];
         controlsConfig[DistributedSwitchesPageComponent.VMOTION_PG_NAME_CONTROL_NAME] = [
            this.getDvpgUniqueName(this.uniqueNewDvsNames[0], Service.VMOTION),
            [Validators.required, Validators.maxLength(80), this.validateDuplicateDvpg],
         ];
      }

      if (this.clusterConfigData.basicConfig.vsanEnabled) {
         controlsConfig[this.VSAN_PG_CONTROL_NAME] =
               [DistributedSwitchesPageComponent.PG_DEFAULT_SWITCH, null];
         controlsConfig[DistributedSwitchesPageComponent.VSAN_PG_NAME_CONTROL_NAME] = [
            this.getDvpgUniqueName(this.uniqueNewDvsNames[0], Service.VSAN),
            [Validators.required, Validators.maxLength(80), this.validateDuplicateDvpg],
         ];
      }

      const pgFormGroup: FormGroup = this.formBuilder.group(controlsConfig);

      const adaptersFormGroups: FormGroup[] = [];
      for (const physicalAdapter of this.physicalAdapters) {
         adaptersFormGroups.push(this.formBuilder.group(
               {
                  [physicalAdapter.deviceName]:
                        [DistributedSwitchesPageComponent.ADAPTER_NOT_IN_USE, null],
               }));
      }

      const adaptersFormArray: FormArray = this.formBuilder.array(adaptersFormGroups);

      const netConfigFormGroup: FormGroup = this.formBuilder.group({
         [DistributedSwitchesPageComponent.DVS_COUNT_CONTROL_NAME]:
               [DistributedSwitchesPageComponent.DVS_COUNT_DEFAULT_VALUE, [Validators.required]],
         [DistributedSwitchesPageComponent.DVS_FORM_ARRAY]: dvsFormArray,
         [DistributedSwitchesPageComponent.PG_FORM_GROUP]: pgFormGroup,
         [DistributedSwitchesPageComponent.ADAPTERS_FORM_ARRAY]: adaptersFormArray,
      });

      this.distributedSwitchesForm = this.formBuilder.group({
         [DistributedSwitchesPageComponent.OPT_OUT_OF_NET_CONFIG_CONTROL_NAME]:
               [this.configureWizardData.optOutOfNetworking, [Validators.required]],
         [DistributedSwitchesPageComponent.NET_CONFIG_FORM_GROUP]: netConfigFormGroup,
      });

      if (this.configureWizardData.optOutOfNetworking) {
         this.disableNetworking(true);
         if (this.configureWizardData.optOutOfNetworkingDisabled) {
            this.optOutOfNetConfigControl.disable();
         }
      }

      this.setDvsNames();
   };

   private registerFormControlChangeHandlers = (): void => {
      this.optOutOfNetConfigControl.valueChanges.forEach(this.disableNetworking);
      this.dvsCountControl.valueChanges.forEach(this.changeDvsCount);

      this.pgFormControls.forEach((control) => {
         control.valueChanges.forEach(this.validateDvsNameControls);
      });

      this.adapterFormControls.forEach((control) => {
         control.valueChanges.forEach(this.validateDvsNameControls);
      });

      this.dvsNameControls.forEach((control) => {
         control.valueChanges.forEach(() => this.pageControls.rebuildToc());
      });

      if (this.clusterConfigData.basicConfig.drsEnabled) {
         this.vmotionPgNameFormControl.valueChanges.forEach(() => this.pageControls.rebuildToc());
      }

      if (this.clusterConfigData.basicConfig.vsanEnabled) {
         this.vsanPgNameFormControl.valueChanges.forEach(() => this.pageControls.rebuildToc());
      }
   };

   private validateDvsNameControls = (): void => {
      ReactiveFormUtil.validateControls(this.dvsNameControls);
      this.pageControls.rebuildToc();
   };

   private validateDvpgNameControls = (): void => {
      if (this.clusterConfigData.basicConfig.drsEnabled) {
         ReactiveFormUtil.validateControls([this.vmotionPgNameFormControl]);
      }

      if (this.clusterConfigData.basicConfig.vsanEnabled) {
         ReactiveFormUtil.validateControls([this.vsanPgNameFormControl]);
      }

      this.pageControls.rebuildToc();
   };

   /** Form control getters */

   get optOutOfNetConfigControl(): FormControl {
      return this.distributedSwitchesForm.get(
            DistributedSwitchesPageComponent.OPT_OUT_OF_NET_CONFIG_CONTROL_NAME) as FormControl;
   }

   get netConfigFormGroup(): FormGroup {
      return this.distributedSwitchesForm.get(DistributedSwitchesPageComponent.NET_CONFIG_FORM_GROUP) as FormGroup;
   }

   get dvsCountControl(): FormControl {
      return this.distributedSwitchesForm.get(DistributedSwitchesPageComponent.DVS_COUNT_CONTROL_PATH) as FormControl;
   }

   get dvsFormArray(): FormArray {
      return this.distributedSwitchesForm.get(DistributedSwitchesPageComponent.DVS_FORM_ARRAY_PATH) as FormArray;
   }

   get dvsNameFormGroups(): FormGroup[] {
      return this.dvsFormArray.controls as FormGroup[];
   }

   get dvsNameControls(): FormControl[] {
      return this.dvsNameFormGroups.map((group) =>
            group.get(DistributedSwitchesPageComponent.DVS_CONTROL_NAME) as FormControl);
   }

   getDvsNameControl = (idx: number): FormControl => {
      const controlPath: string = ReactiveFormUtil.getFormControlPath(
            DistributedSwitchesPageComponent.DVS_FORM_ARRAY_PATH,
            idx.toString(),
            DistributedSwitchesPageComponent.DVS_CONTROL_NAME);
      return this.distributedSwitchesForm.get(controlPath) as FormControl;
   };

   get pgFormGroup(): FormGroup {
      return this.distributedSwitchesForm.get(DistributedSwitchesPageComponent.PG_FORM_GROUP_PATH) as FormGroup;
   }

   get pgFormControls(): FormControl[] {
      const controls: any = this.pgFormGroup.controls;
      return Object.keys(controls).map((key) => controls[key]);
   }

   get adapterFormArray(): FormArray {
      return this.distributedSwitchesForm.get(DistributedSwitchesPageComponent.ADAPTERS_FORM_ARRAY_PATH) as FormArray;
   }

   get adapterFormGroups(): FormGroup[] {
      return this.adapterFormArray.controls as FormGroup[];
   }

   get adapterFormControls(): FormControl[] {
      return this.adapterFormGroups
            .map((group) => group.controls[Object.keys(group.controls)[0]] as FormControl);
   }

   get vmotionPgFormControl(): FormControl {
      const controlPath: string = ReactiveFormUtil.getFormControlPath(
            DistributedSwitchesPageComponent.NET_CONFIG_FORM_GROUP,
            DistributedSwitchesPageComponent.PG_FORM_GROUP,
            this.VMOTION_PG_CONTROL_NAME);
      return this.distributedSwitchesForm.get(controlPath) as FormControl;
   }

   get vmotionPgNameFormControl(): FormControl {
      const controlPath: string = ReactiveFormUtil.getFormControlPath(
            DistributedSwitchesPageComponent.NET_CONFIG_FORM_GROUP,
            DistributedSwitchesPageComponent.PG_FORM_GROUP,
            DistributedSwitchesPageComponent.VMOTION_PG_NAME_CONTROL_NAME);
      return this.distributedSwitchesForm.get(controlPath) as FormControl;
   }

   get vsanPgFormControl(): FormControl {
      const controlPath: string = ReactiveFormUtil.getFormControlPath(
            DistributedSwitchesPageComponent.NET_CONFIG_FORM_GROUP,
            DistributedSwitchesPageComponent.PG_FORM_GROUP,
            this.VSAN_PG_CONTROL_NAME);
      return this.distributedSwitchesForm.get(controlPath) as FormControl;
   }

   get vsanPgNameFormControl(): FormControl {
      const controlPath: string = ReactiveFormUtil.getFormControlPath(
            DistributedSwitchesPageComponent.NET_CONFIG_FORM_GROUP,
            DistributedSwitchesPageComponent.PG_FORM_GROUP,
            DistributedSwitchesPageComponent.VSAN_PG_NAME_CONTROL_NAME);
      return this.distributedSwitchesForm.get(controlPath) as FormControl;
   }

   /** Form control properties getters */

   get maxDvsCount(): number {
      return Math.min(DistributedSwitchesPageComponent.NET_MAX_COUNT, this.physicalAdapters.length);
   }

   get dvsCountOptions(): number[] {
      // creates array of type [1, ..., dvsCount]
      return Array.from(new Array(this.maxDvsCount), (value, index) => index + 1);
   }

   get dvsCount(): number {
      return Number(this.dvsCountControl.value);
   }

   get dvsFormValues(): string[] {
      const dvsFormGroups: FormGroup[] = this.dvsFormArray.controls as FormGroup[];
      return dvsFormGroups
            .map((group) => group.get(DistributedSwitchesPageComponent.DVS_CONTROL_NAME).value)
            .filter((name) => !!name);
   }

   // used to reduce the watchers in the component.
   setDvsNames(): void {
      this.dvsNames = this.dvsFormValues;
   }

   get netConfigDisabled(): boolean {
      return this.optOutOfNetConfigControl.value;
   }

   get localDuplicateDvsNames(): string[] {
      return this.dvsFormValues.sort().filter((element, index, array) => array.indexOf(element) !== index);
   }

   getDvsPortGroupsCount = (dvsIndex: number): number => {
      return this.pgFormControls.filter((control) => control.value === String(dvsIndex)).length;
   };

   getDvsUplinksCount = (dvsIndex: number): number => {
      return this.adapterFormControls.filter((control) => control.value === String(dvsIndex)).length;
   };

   getExistingDvsSelected = (): boolean => {
      return this.selectedExistingDvs && this.selectedExistingDvs.some(dvs => dvs && !!dvs.name);
   };

   getExistingDvsWarning(): string {
      let warning: string = VsanUiUtils.getString(
            "vsan.cluster.hci.configureHciCluster.wizard.dvsConfig.existingDvsWarning");

      if (this.clusterConfigData.basicConfig.vsanEnabled
            && this.selectedExistingDvs.some(dvs => dvs && dvs.name === this.getStorageDvsName())) {
         warning = warning.concat(VsanUiUtils.getString(
               "vsan.cluster.hci.configureHciCluster.wizard.dvsConfig.existingDvsVsanWarning"));
      }

      return warning;
   }

   /** Form validation */

   private validateDuplicateDvsNames = (dvsFormArray: FormArray): { [key: string]: any } | null => {
      if (!dvsFormArray.parent) {
         return null;
      }

      // check for local duplicates only if there are more than one DVSes
      if (this.dvsCount > 1 && this.localDuplicateDvsNames.length) {
         return {
            [DistributedSwitchesPageComponent.DVS_LOCALLY_DUPLICATE_NAMES_VALIDATION]: {
               valid: false,
               duplicateNames: this.localDuplicateDvsNames,
            },
         };
      }

      let duplicateNames: string[] =
            this.dvsFormValues.filter((element) => this.existingDvsNames.indexOf(element) !== -1);

      if (this.selectedExistingDvs && this.selectedExistingDvs.length) {
         duplicateNames = duplicateNames.filter((element) => {
            return this.selectedExistingDvs.map((elem) => elem ? elem.name : "").indexOf(element) === -1;
         });
      }

      if (duplicateNames.length) {
         return {
            [DistributedSwitchesPageComponent.DVS_EXISTING_DUPLICATE_NAMES_VALIDATION]: {
               valid: false,
               duplicateNames: duplicateNames,
            },
         };
      }

      return null;
   };

   private validateDuplicateDvpg = (dvpgNameControl: AbstractControl): { [key: string]: any } | null => {
      if (!this.existingPgNames || !dvpgNameControl || !dvpgNameControl.parent) {
         return null;
      }

      if ((this.existingVmotionDvpg && this.existingVmotionDvpg.name === dvpgNameControl.value) ||
            (this.existingStorageDvpg && this.existingStorageDvpg.name === dvpgNameControl.value)) {
         return null;
      }

      if (this.getVmotionDvpgName() === this.getStorageDvpgName()) {
         return {
            [DistributedSwitchesPageComponent.DVPG_LOCALLY_DUPLICATE_NAMES_VALIDATION]: {
               valid: false,
            },
         };
      }

      return !this.existingPgNames.includes(dvpgNameControl.value.trim()) ? null : {
         [DistributedSwitchesPageComponent.DVPG_EXISTING_DUPLICATE_NAMES_VALIDATION]: {
            valid: false,
         },
      };
   };

   private validateAssignment = (dvsNameFormControl: FormControl): { [key: string]: any } | null => {
      // validators are run before wiring the form
      if (!dvsNameFormControl.parent) {
         return null;
      }

      const groupIndex: number = this.getDvsNameGroupIndex(dvsNameFormControl.parent as FormGroup);

      const result: any = {};
      const assignedAdapters: FormControl[] = this.adapterFormControls.filter(
            (control) => control.value === String(groupIndex));
      if (!assignedAdapters.length) {
         result[DistributedSwitchesPageComponent.DVS_TO_ADAPTER_ASSIGNMENT_VALIDATION] = {valid: false};
      }

      return result;
   };

   private getDvsNameGroupIndex = (formGroup: FormGroup): number => {
      const array: FormArray = formGroup.parent as FormArray;
      for (let idx: number = 0; idx < array.controls.length; idx++) {
         if (array.at(idx) === formGroup) {
            return idx;
         }
      }

      Logger.error("Controls configuration problem, ");
   };

   private disableNetworking = (optOutOfNetConfig: boolean) => {
      this.configureWizardData.optOutOfNetworking = optOutOfNetConfig;
      if (optOutOfNetConfig) {
         this.netConfigFormGroup.disable();
      } else {
         this.netConfigFormGroup.enable();
      }

      this.pageControls.rebuildToc();
   };

   private changeDvsCount = (dvsCount: number): void => {
      const dvsFormArray: FormArray =
            this.createDvsFormArray(dvsCount, this.dvsFormValues);
      const netSettingsFormGroup: FormGroup =
            this.distributedSwitchesForm.get(DistributedSwitchesPageComponent.NET_CONFIG_FORM_GROUP) as FormGroup;
      netSettingsFormGroup.setControl(DistributedSwitchesPageComponent.DVS_FORM_ARRAY, dvsFormArray);

      // DVSes are reduced - resetting the selections that are no valid any more
      if (dvsCount < this.maxDvsCount) {
         const highestDvsIndex: number = dvsCount - 1;
         this.setPortGroupsDvs(String(0), highestDvsIndex);
         this.setAdaptorsDvs(DistributedSwitchesPageComponent.ADAPTER_NOT_IN_USE, highestDvsIndex);
         this.selectedExistingDvs = this.selectedExistingDvs.slice(0, dvsCount);
      }

      this.dvsNameControls.forEach((control) => {
         control.valueChanges.forEach(() => this.pageControls.rebuildToc());
      });

      this.setDvsNames();

      this.pageControls.rebuildToc();
   };

   private setPortGroupsDvs = (newDvsName: string, marginDvsName: number): void => {
      const controls: any = this.pgFormGroup.controls;
      Object.keys(controls)
            .map((key) => controls[key])
            .map((control) => {
               if (+control.value > +marginDvsName) {
                  control.setValue(newDvsName);
               }
            });
   };

   private setAdaptorsDvs = (newDvsName: string, marginDvsName: number): void => {
      this.adapterFormGroups.map((group) => {
         const control: FormControl = group.controls[Object.keys(group.controls)[0]] as FormControl;
         if (+control.value > +marginDvsName) {
            control.setValue(newDvsName);
         }
      });
   };

   private createDvsFormArray = (dvsCount: number, dvsNames?: string[]): FormArray => {
      const dvsFormGroups: FormGroup[] = [];
      let existingDvsSelected: number = 0;
      for (let idx: number = 0; idx < dvsCount; idx++) {
         let dvsName: string;
         let isExistingDvs: boolean;
         if (this.selectedExistingDvs[idx]) {
            dvsName = this.selectedExistingDvs[idx].name;
            existingDvsSelected++;
            isExistingDvs = true;
         } else if (dvsNames && dvsNames.length > idx) {
            dvsName = this.dvsFormValues[idx];
         } else {
            dvsName = this.uniqueNewDvsNames[idx - existingDvsSelected];
         }

         const validators: any[] = isExistingDvs ? [] : [
            this.validateAssignment,
            Validators.required,
            Validators.maxLength(80),
         ];

         dvsFormGroups.push(
               this.formBuilder.group({
                  [DistributedSwitchesPageComponent.DVS_CONTROL_NAME]: [dvsName, validators],
               }),
         );
      }

      return this.formBuilder.array(dvsFormGroups, this.validateDuplicateDvsNames);
   };

   private getVmotionDvsName = (): string => {
      return this.vmotionPgFormControl && this.vmotionPgFormControl.value ?
            this.getDvsNameControl(this.vmotionPgFormControl.value).value : "";
   };

   private getStorageDvsName = (): string => {
      return this.vsanPgFormControl && this.vsanPgFormControl.value
            ? this.getDvsNameControl(this.vsanPgFormControl.value).value : "";
   };

   private getVmotionDvpgName = (): string => {
      return this.vmotionPgNameFormControl ? this.vmotionPgNameFormControl.value : "";
   };

   private getStorageDvpgName = (): string => {
      return this.vsanPgNameFormControl ? this.vsanPgNameFormControl.value : "";
   };

   validate(): string[] | Promise<string[]> {
      const dvsAssignmentErrors: string[] =
            this.validator.getDvsAssignmentErrors(this.dvsNameControls);

      if (dvsAssignmentErrors.length) {
         return dvsAssignmentErrors;
      }

      if (!this.configureWizardData.optOutOfNetworking) {
         const hasInvalidDvsName: boolean = this.dvsNameControls
               .some(control => this.validator.invalidDvsNameControl(control));
         ReactiveFormUtil.validateAllFormFields(this.distributedSwitchesForm);
         if (this.distributedSwitchesForm.invalid || hasInvalidDvsName) {
            return [
               VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.networking.notAcceptableErrors"),
            ];
         }
         return this.validatePermissions();
      }

      return this.populateSpec();
   }

   private validatePermissions(): Promise<any> {
      const permissionPromises: Promise<any>[] = [];

      // check permissions for creating new DVS/DVPG
      const checkDvsCreatePermission: boolean = this.dvsFormValues
            .some((dvsName) => {
               return !this.selectedExistingDvs.some(dvs => dvs && dvs.name === dvsName);
            });

      const checkDvpgCreatePermission: boolean =
            (this.clusterConfigData.basicConfig.drsEnabled && !this.existingVmotionDvpg)
            || (this.clusterConfigData.basicConfig.vsanEnabled && !this.existingStorageDvpg);
      if (checkDvsCreatePermission || checkDvpgCreatePermission) {
         permissionPromises.push(this.hciClusterService.hasNetworkingCreatePermissions(
               this.clusterRef, checkDvsCreatePermission, checkDvpgCreatePermission));
      }

      // check permissions for existing DVS/DVPG
      if ((this.selectedExistingDvs && this.selectedExistingDvs.length)
            || this.existingStorageDvpg || this.existingVmotionDvpg) {
         // get selected existing dvsMors
         const dvsMors: ManagedObject[] = this.selectedExistingDvs.filter(dvs => !!dvs).map(dvs => dvs.dvsRef);
         const dvpgMors: ManagedObject[] = this.existingStorageDvpg ? [this.existingStorageDvpg.dvpgRef] : [];
         if (this.existingVmotionDvpg) {
            dvpgMors.push(this.existingVmotionDvpg.dvpgRef);
         }

         permissionPromises.push(this.hciClusterService.hasNetworkingModifyPermissions(dvsMors, dvpgMors));
      }

      return Promise.all(permissionPromises).then((result) => {
         // if any permission is not granted, show error
         if (result && result.length && (result[0] === false || result[1] === false)) {
            return [VsanUiUtils.getString(
                  "vsan.cluster.hci.configureHciCluster.wizard.dvsConfig.permissionErrorMessage")];
         }

         return this.populateSpec();
      });
   }

   private populateSpec = async (): Promise<string[]> => {
      // prepare output parameters
      if (this.clusterConfigData.basicConfig.drsEnabled) {
         this.vmotionDvsName = this.getVmotionDvsName();
         if (this.existingVmotionDvpg) {
            this.vmotionDvpg = DvpgData.Factory.create(this.existingVmotionDvpg.name, this.existingVmotionDvpg.dvpgRef);
         } else {
            this.vmotionDvpg = DvpgData.Factory.create(this.getVmotionDvpgName(), null);
         }
      }

      if (this.clusterConfigData.basicConfig.vsanEnabled) {
         this.storageDvsName = this.getStorageDvsName();
         if (this.existingStorageDvpg) {
            this.storageDvpg = DvpgData.Factory.create(this.existingStorageDvpg.name, this.existingStorageDvpg.dvpgRef);
         } else {
            this.storageDvpg = DvpgData.Factory.create(this.getStorageDvpgName(), null);
         }
      }

      // fill the cluster configure spec
      this.clusterConfigData.optOutOfNetConfig = this.configureWizardData.optOutOfNetworking;
      if (!this.configureWizardData.optOutOfNetworking) {
         this.clusterConfigData.dvsSpecs = this.dvsFormArray.controls.map((group, idx) => {
            const control: FormControl = group.get(DistributedSwitchesPageComponent.DVS_CONTROL_NAME) as FormControl;
            const dvsMor: ManagedObject | null =
                  this.selectedExistingDvs[idx] ? this.selectedExistingDvs[idx].dvsRef : null;
            return DvsSpec.Factory.create(
                  control.value,
                  this.getDvsServices(idx),
                  this.getDvsHostAdapters(idx),
                  dvsMor);
         });

         const validationMessages: string[] = await this.hciClusterService
               .validateNetworkSpecification(this.clusterRef, this.clusterConfigData);
         // Empty array is considered as invalid validation result
         return validationMessages?.length ? validationMessages : null;
      }

      return null;
   };

   private getDvsServices = (dvsIdx: number): Service[] => {
      const controls: any = this.pgFormGroup.controls;
      return Object.keys(controls)
            .filter((key) => (controls[key].value) === String(dvsIdx))
            .map((key) => key as Service);
   };

   private getDvsHostAdapters = (dvsIdx: number): HostAdapter[] => {
      const groups: FormGroup[] = this.adapterFormGroups;
      return groups
            .map((group) => {
               const controlsObject: { [key: string]: AbstractControl } = group.controls;
               const deviceName: string = Object.keys(controlsObject)[0];
               const hostAdapter: HostAdapter = this.getHostAdapter(deviceName);
               if (controlsObject[deviceName].value === String(dvsIdx)) {
                  hostAdapter.dvsName = this.getDvsNameControl(controlsObject[deviceName].value).value;
                  return hostAdapter;
               }

               return null;
            })
            .filter((element) => element != null);
   };

   private getHostAdapter = (deviceName: string): HostAdapter => {
      return this.physicalAdapters.find((adapter) => adapter.deviceName === deviceName);
   };

   navigateToVvd(): void {
      this.navigationService.navigateToVmwareCom(DistributedSwitchesPageComponent.VVD_ROUTE);
   }

   onVmotionDvsChange(dvsIdx: number): void {
      const dvsName: string = this.getDvsNameControl(dvsIdx).value;
      this.existingVmotionDvpg = null;
      this.showExistingVmotionButton = this.selectedExistingDvs.some(dvs => dvs && dvs.name === dvsName);
      this.vmotionPgNameFormControl.setValue(this.getDvpgUniqueName(dvsName, Service.VMOTION));
   }

   onVsanDvsChange(dvsIdx: number): void {
      const dvsName: string = this.getDvsNameControl(dvsIdx).value;
      this.existingStorageDvpg = null;
      this.showExistingVsanButton = this.selectedExistingDvs.some(dvs => dvs && dvs.name === dvsName);
      this.vsanPgNameFormControl.setValue(this.getDvpgUniqueName(dvsName, Service.VSAN));
   }

   populateDvpgName = (dvsName: string, showExistingButton?: boolean) => {
      if (dvsName === this.getVmotionDvsName() && this.vmotionPgNameFormControl) {
         this.showExistingVmotionButton = showExistingButton;
         this.vmotionPgNameFormControl.setValue(this.getDvpgUniqueName(dvsName, Service.VMOTION));
      }

      if (dvsName === this.getStorageDvsName() && this.vsanPgNameFormControl) {
         this.showExistingVsanButton = showExistingButton;
         this.vsanPgNameFormControl.setValue(this.getDvpgUniqueName(dvsName, Service.VSAN));
      }

      this.setDvsNames();
   };

   openExistingDvsDialog(dvsIdx: number): void {
      const dvsName: string = this.selectedExistingDvs && this.selectedExistingDvs[dvsIdx]
            ? this.selectedExistingDvs[dvsIdx].name : "";
      new ModalBuilder(this.HCI_WIZARD_ROUTE_PATH + this.EXISTING_DVS_SELECTOR_COMPONENT)
            .setSize(800, 450)
            .open({
               clusterRef: this.clusterRef,
               selectedExistingDvsName: dvsName,
               selectedDvsNames: this.selectedExistingDvs.map(dvs => dvs ? dvs.name : ""),
            })
            .then((existingDvsSelection: ExistingDvsData | null): void => {
               if (this.getVmotionDvsName() === dvsName) {
                  this.existingVmotionDvpg = null;
               }

               if (this.getStorageDvsName() === dvsName) {
                  this.existingStorageDvpg = null;
               }

               if (existingDvsSelection && this.selectedExistingDvs.indexOf(existingDvsSelection) === -1) {
                  this.selectedExistingDvs[dvsIdx] = existingDvsSelection;
                  const newDvsName: string = existingDvsSelection.name;
                  this.dvsNameControls[dvsIdx].setValue(newDvsName);
                  this.populateDvpgName(newDvsName, true);
               } else if (!existingDvsSelection) {
                  this.selectedExistingDvs[dvsIdx] = null;
                  const newDvsName: string = this.uniqueNewDvsNames.find((el) => this.dvsFormValues.indexOf(el) === -1);
                  this.dvsNameControls[dvsIdx].setValue(newDvsName);
                  this.populateDvpgName(newDvsName, false);
               }

               this.setDvsNames();
            });
   }

   openExistingDvpgVmotionDialog(): void {
      const existingDvs: ExistingDvsData = this.selectedExistingDvs.find(dvs => dvs
            && dvs.name === this.getVmotionDvsName());

      const networkName: string = this.existingVmotionDvpg && this.existingVmotionDvpg.name
            ? this.existingVmotionDvpg.name : "";

      const dialogPromise: Promise<ExistingDvpgData | null> =
            this.openExistingDvpgDialog(existingDvs.dvsRef, networkName);

      dialogPromise.then((existingDvpgSelection: ExistingDvpgData | null): void => {
         this.existingVmotionDvpg = existingDvpgSelection;
         if (existingDvpgSelection) {
            this.vmotionPgNameFormControl.setValue(this.existingVmotionDvpg.name);
         } else {
            this.populateDvpgName(existingDvs.name, true);
         }

         this.validateDvpgNameControls();
      });
   }

   openExistingDvpgVsanDialog(): void {
      const existingDvs: ExistingDvsData = this.selectedExistingDvs.find(dvs => dvs
            && dvs.name === this.getStorageDvsName());

      const networkName: string = this.existingStorageDvpg && this.existingStorageDvpg.name
            ? this.existingStorageDvpg.name : "";

      const dialogPromise: Promise<ExistingDvpgData | null> =
            this.openExistingDvpgDialog(existingDvs.dvsRef, networkName);

      dialogPromise.then((existingDvpgSelection: ExistingDvpgData | null): void => {
         this.existingStorageDvpg = existingDvpgSelection;
         if (existingDvpgSelection) {
            this.vsanPgNameFormControl.setValue(this.existingStorageDvpg.name);
         } else {
            this.populateDvpgName(existingDvs.name, true);
         }

         this.validateDvpgNameControls();
      });
   }

   private getDvpgUniqueName(dvsName: string, service: Service): string {
      return StringUtil.createUniqueIndexedString(
            this.existingPgNames,
            this.getDefaultDvpgName(service, dvsName),
            DistributedSwitchesPageComponent.DVS_SERVICE_WORD_SEPARATOR);
   }

   private openExistingDvpgDialog(
         dvsRef: ManagedObject, selectedExistingDvpgName: string): Promise<ExistingDvpgData | null> {
      return new ModalBuilder(this.HCI_WIZARD_ROUTE_PATH + this.EXISTING_DVPG_SELECTOR_COMPONENT)
            .setSize(800, 450)
            .open({
               dvsRef: dvsRef,
               selectedExistingDvpgName: selectedExistingDvpgName,
            });
   }

   private getDefaultDvpgName(service: Service, dvsName: string): string {
      let dvpgName: string = dvsName + DistributedSwitchesPageComponent.DVS_SERVICE_SEPARATOR;
      switch (service) {
         case Service.VSAN:
            dvpgName += DistributedSwitchesPageComponent.VSAN_PG_NAME;
            break;
         case Service.VMOTION:
            dvpgName += DistributedSwitchesPageComponent.VMOTION_PG_NAME;
            break;
      }

      return dvpgName;
   }
}
