
export interface DvpgData {

   name: string;
   existingDvpg: any;
} //class DvpgData

export module DvpgData{
export class Factory {

   static create = (name: string, existingDvpg: any): DvpgData => {
      return { name: name, existingDvpg: existingDvpg, };
   }


} //class Factory

} //module DvpgData
