import {Injectable} from "@angular/core";
import {ClusterConfigData} from "@generated/cluster-config-data";
import {ConfigureWizardData} from "@generated/configure-wizard-data";
import {HciClusterService} from "@generated/hci-cluster-service";
import {IManagedObject} from "@service/client/core/imanaged-object.data";
import {ManagedObject} from "@service/managed-object";
import {tsService} from "@service/service-metadata";
import {Logger} from "@util/logger";
import {VsanQuickstartUtil} from "@util/vsan-quickstart-util";
import {SupportInsightConfigurationService} from "./support-insight-configuration.service";

@Injectable()
export class ConfigureHciService {

   constructor(private supportInsightService: SupportInsightConfigurationService,
         private hciClusterService: HciClusterService) {
   }

   @tsService("configureHciCluster")
   async configureSupportInsight(configureWizardData: ConfigureWizardData,
         clusterConfigData: ClusterConfigData): Promise<IManagedObject> {
      // Only save support insight related configurations in cluster creation step
      if (VsanQuickstartUtil.isHciWorkflowInProgress(clusterConfigData.basicConfig.hciWorkflowState)) {
         try {
            await this.supportInsightService.configureSupportInsight(configureWizardData, clusterConfigData);
         } catch (e) {
            // Don't break the HCI Configure workflow because of the support insight failure.
            Logger.warn(e);
         }
      }
      return this.hciClusterService.configureCluster(ManagedObject.contextObject, clusterConfigData);
   }
}
