import {Component} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {PageControls} from "@component/wizard/flow-wizard.component";
import {VsanUiUtils} from "@util/vsan-util";
import {ConfigureWizardData} from "@generated/configure-wizard-data";

@WizardPage(
      VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.dialog.supportInsight.tab.title"),
      VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.dialog.supportInsight.title"),
)
@Component({
   templateUrl: "support-insight-page.html",
   styleUrls: ["support-insight-page.scss"],
})
export class SupportInsightPageComponent {

   @InputProperty()
   pageControls: PageControls;

   @InputProperty()
   @OutputProperty()
   configureWizardData: ConfigureWizardData;

   private _hideSupportInsight: boolean;

   get hideSupportInsight(): boolean {
      return this._hideSupportInsight;
   }

   set hideSupportInsight(value: boolean) {
      this._hideSupportInsight = value;
      this.configureWizardData.isSupportInsightStepHidden = value;
      this.pageControls.rebuildToc();
   }

   private _disableSupportInsight: boolean = false;

   get disableSupportInsight(): boolean {
      return this._disableSupportInsight;
   }

   set disableSupportInsight(val: boolean) {
      this._disableSupportInsight = val;
      this.configureWizardData.disableSupportInsight = val;
      // The hide checkbox only makes sense if the service is disabled, so reset it's state if it is enabled.
      if (!val) {
         this.hideSupportInsight = false;
      }
      this.pageControls.rebuildToc();
   }
}