import {AfterViewInit, Component, OnInit, ViewChild} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {VsanUiUtils} from "@util/vsan-util";
import {ConfigureWizardData} from "@generated/configure-wizard-data";
import {ExternalProxySettingsConfig} from "@generated/external-proxy-settings-config";
import {ProxyInputsComponent, ProxySetting} from "@component/proxy-settings/proxy-inputs.component";
import {ReferenceWatcher} from "@util/reference-watcher";

@WizardPage(
      VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.dialog.vcproxy.tab.title"),
)
@Component({
   templateUrl: "proxy-settings-page.html",
   styleUrls: ["proxy-settings-page.scss"],
   providers: [ReferenceWatcher],
})
export class ProxySettingsPageComponent implements AfterViewInit, OnValidate {
   private static readonly HTTP_PREFIX: string = "http://";
   private static readonly HTTPS_PREFIX: string = "https://";

   @ViewChild(ProxyInputsComponent)
   proxyInputs: ProxyInputsComponent;

   @InputProperty()
   @OutputProperty()
   configureWizardData: ConfigureWizardData;

   @InputProperty()
   pageControls: PageControls;

   enableProxy: boolean;

   constructor(private refWatcher: ReferenceWatcher) {
   }

   ngAfterViewInit(): void {
      // Invalidate the page whenever there is a change in the inner form.
      this.refWatcher.watchSubscriptionRef(
            this.proxyInputs.proxyForm.valueChanges.subscribe(this.pageControls.rebuildToc));
   }

   validate(): string[] {
      this.pageControls.clearValidationErrors();
      if (this.proxyInputs && this.proxyInputs.proxyForm && this.proxyInputs.proxyForm.invalid) {
         return [VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.wizard.networking.notAcceptableErrors")];
      }
      // Populate the spec only when inner form validation is passed
      this.populateSpec();
   }

   /**
    * the vAPI need a valid URL for the hostname, so we must check the protocol here
    */
   private resolveProxyUrl = (hostName: string): string => {
      if (!hostName.startsWith(ProxySettingsPageComponent.HTTP_PREFIX)
            && !hostName.startsWith(ProxySettingsPageComponent.HTTPS_PREFIX)) {
         return ProxySettingsPageComponent.HTTPS_PREFIX + hostName;
      }
      return hostName;
   };

   private populateSpec(): void {
      this.configureWizardData.proxySettingsConfig = ExternalProxySettingsConfig.Factory.create(
            false, null, null, null, null, true);
      const proxySettings: ProxySetting = this.proxyInputs.value();
      if (proxySettings && proxySettings.enableProxy) {
         this.configureWizardData.proxySettingsConfig.hostName = this.resolveProxyUrl(proxySettings.hostname);
         this.configureWizardData.proxySettingsConfig.port = proxySettings.port;
         this.configureWizardData.proxySettingsConfig.userName = proxySettings.username;
         this.configureWizardData.proxySettingsConfig.password = proxySettings.password;
      }
   }
}