import {VsanUiUtils} from "@util/vsan-util";
import {VsanConfigSpec} from "@generated/vsan-config-spec";

export class VsanDitUtil {
   static readonly DEFAULT_DIT_REKEY_INTERVAL: number = 24 * 60;

   static getDitUnavailableMessage(vsanConfigSpec: VsanConfigSpec,
         isClusterClientOrServer: boolean,
         hasSharedWitnessHost: boolean,
         isDitSharedWitnessInteroperabilitySupported: boolean): string {

      if (vsanConfigSpec.autoClaimDisks) {
         return VsanUiUtils.getString("vsan.dataInTransitEncryption.unavailable.autoClaimDisks");
      }

      if (isClusterClientOrServer) {
         return VsanUiUtils.getString("vsan.dataInTransitEncryption.unavailable.isServerOrClient");
      }

      if (hasSharedWitnessHost && !isDitSharedWitnessInteroperabilitySupported) {
         return VsanUiUtils.getString("vsan.dataInTransitEncryption.unavailable.hasSharedWitness");
      }

   }
}
