import {Injectable} from "@angular/core";
import {IdGenerator} from "@util/id-generator";
import {WebPlatform} from "@util/platform";
import {CookieService} from "ngx-cookie-service";
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {DataAccessService} from "@service/data-access.service";
import {ManagedObject} from "@service/managed-object";
import {Logger} from "@util/logger";
import {VsanUiUtils} from "@util/vsan-util";
import {ConfigureWizardData} from "@generated/configure-wizard-data";
import {ClusterConfigData} from "@generated/cluster-config-data";
import {ExternalProxySettingsConfig} from "@generated/external-proxy-settings-config";
import {NetworkProxyService} from "@service/vapi/network-proxy.service";
import {CeipService} from "@generated/ceip-service";

// TODO [kaleksandrov] Remove cookie-service and start using CookieAdapter
@Injectable()
export class SupportInsightConfigurationService {
   private static readonly CEIP_URL: string = WebPlatform.webContextPath + "/../ceip-ui/ctrl/ceip/status/";
   private static readonly VSPHERE_UI_XSRF_TOKEN_HEADER: string = "X-VSPHERE-UI-XSRF-TOKEN";
   private static readonly VSPHERE_UI_XSRF_TOKEN_COOKIE: string = "VSPHERE-UI-XSRF-TOKEN";

   constructor(private http: HttpClient,
         private networkProxyService: NetworkProxyService,
         private ceipService: CeipService,
         private cookieService: CookieService) {
   }

   async configureSupportInsight(configureWizardData: ConfigureWizardData,
         clusterConfigData: ClusterConfigData): Promise<void> {
      const promises: Promise<any>[] = [];
      const clusterRef: ManagedObject = ManagedObject.contextObject;

      if (clusterConfigData.basicConfig.vsanEnabled
            && !configureWizardData.ceipEnabled
            && !configureWizardData.disableSupportInsight) {
         promises.push(this.joinCeip());
      }
      if (!configureWizardData.disableSupportInsight
            && configureWizardData.proxySettingsConfig?.hostName) {
         promises.push(this.saveProxySettings(configureWizardData.proxySettingsConfig));
      }
      if (configureWizardData.isSupportInsightStepHidden) {
         promises.push(this.ceipService.hideSupportInsightStep(clusterRef));
      }

      try {
         if (promises.length > 0) {
            await Promise.all(promises);
         }
         return;

      } catch (err) {
         Logger.error("Faild to configure vSAN Support Insight.", err);
         return Promise.reject(err);
      }
   }

   private joinCeip(): Promise<void> {
      const dataUrl: string = SupportInsightConfigurationService.CEIP_URL + true;
      return this.doRequest(dataUrl, HTTP_METHOD.PUT, null, this.prepareCeipHeader());
   }

   /**
    * To enable/disable the ceip service successfully via the REST API, this xsrf token is required
    */
   private prepareCeipHeader(): { headers: HttpHeaders } {
      const options: { headers: HttpHeaders } = DataAccessService.getDsRequestOptions();
      const xsrfToken: string =
            this.cookieService.get(SupportInsightConfigurationService.VSPHERE_UI_XSRF_TOKEN_COOKIE);
      options.headers =
            options.headers.append(SupportInsightConfigurationService.VSPHERE_UI_XSRF_TOKEN_HEADER, xsrfToken);
      return options;
   }

   /**
    * Save the VC proxy settings for https connection to be used by Support Insight service
    */
   private saveProxySettings(proxySettings: ExternalProxySettingsConfig): Promise<void> {
      return this.networkProxyService.updateSettings(ManagedObject.contextObject, true, proxySettings.hostName,
            proxySettings.port, proxySettings.userName, proxySettings.password)
            .catch(err => Logger.error("Failed to save VC proxy settings", err));
   }

   private doRequest = (dataUrl: string,
         httpMethod: HTTP_METHOD,
         body?: any,
         opts?: { headers: HttpHeaders }): Promise<any> => {
      opts = opts || DataAccessService.getDsRequestOptions();
      return new Promise<any>((accept, reject) => {
         const date: Date = new Date();
         const time: number = date.getTime();
         const requestId: string = IdGenerator.getUniqueRequestId();
         Logger.debug(requestId + " [" + date.toLocaleString() + "]", dataUrl);

         const encodedDataUrl: string = encodeURI(dataUrl);
         let promise: Promise<any> = null;
         switch (httpMethod) {
            case HTTP_METHOD.GET:
               promise = this.http.get(encodedDataUrl, opts).toPromise();
               break;
            case HTTP_METHOD.PUT:
               let requestJson: string;
               if (body) {
                  requestJson = JSON.stringify(body);
               }
               promise = this.http.put(encodedDataUrl, requestJson, opts).toPromise();
               break;
            case HTTP_METHOD.POST:
               promise = this.http.post(encodedDataUrl, null, opts).toPromise();
               break;
            default:
               const error: string = "Invalid request found: " + httpMethod;
               Logger.warn(error);
               reject(error);
         }
         if (promise) {
            promise.then((r) => {
               const duration: number = new Date().getTime() - time;
               Logger.debug(requestId + " [" + duration + "ms]:", r);
               accept(r);
            }).catch((error) => {
               Logger.error(requestId + ":", error);
               reject(error.message ? error.message : VsanUiUtils.getString("vsan.common.error.taskError"));
            });
         }
      });
   };
}

export enum HTTP_METHOD {
   GET, PUT, POST,
}
